import './ScoreContLibraryTab.css';

// import TabbedPane from './common/TabbedPane.jsx';
import { DetailSection, CHORD_MODE, Store, TypeNoteElement, EditMode, NoteData, NoteElement } from 'contents/redux/store';
import { GlobalContext } from 'contents/MainFrame';
import React, { useContext, KeyboardEvent, useEffect, useState } from 'react';
import { ActionType } from 'contents/redux/reducer';
import { API_DOMAIN } from 'contents/common/define';
import { createQueryRequestInit, sendQueryRequestToAPI } from 'contents/common/utils';

type Record = { id: number, title: string, chord_mode: CHORD_MODE, data_cnt: number };

const ScoreContLiblaryTab = () => {
    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    const [focus, setFocus] = useState<number>(-1);
    const [message, setMessage] = useState<string>('');
    const [recordList, setRecordList] = useState<Record[]>([]);
    const [titleList, setTitleList] = useState<JSX.Element[]>([]);
    const [isEdit, setEdit] = useState<boolean>(false);
    const [title, setTitle] = useState<string>('');
    const [chordMode, setChordMode] = useState<CHORD_MODE | ''>('');
    const [isLoad, setLoad] = useState<boolean>(false);

    useEffect(() => {
        console.log('create ScoreContLiblaryTab');
    }, []);

    // 
    const reload = (initFocus: number) => {
        // setFocus(initFocus);
        setLoad(true);
        setMessage('Now roading scores...');
        sendQueryRequestToAPI('select', `SELECT s.*, (select count(*) from data_tbl where score = s.id) as data_cnt FROM sclibtbl s where user = ${state.login.id}`)
            .then(response => {
                return response.json();
            })
            .then(results => {
                console.log(`[${state.login.id}]score count: ` + results.length);
                const isExist = results.length > 0;
                if (isExist) {
                    setRecordList(results);
                    setMessage('');
                } else {
                    setMessage('There is no musical score you manage yet.');
                }
                setLoad(false);
            })
            .catch(error => {
                console.log(error + "");
                setLoad(false);
            });
    }

    useEffect(() => {
        const list: JSX.Element[] = [];
        recordList.forEach((record, i) => {
            list.push(
                <div
                    key={i}
                    className={[
                        'record',
                        focus == i ? 'focus' : ''
                    ].join(' ')}
                    onClick={() => {
                        setFocus(i);
                    }}
                >
                    <span>{record.id}:</span>
                    <span className="title">{record.title}</span>
                    <span>[</span>
                    <span className="mode">{record.chord_mode}</span>
                    <span>]</span>
                </div>
            );
        });
        setTitleList(list);
    }, [recordList, focus]);

    useEffect(() => {
        if (!isEdit) {
            reload(-1);
        }

    }, [isEdit]);

    const createButton = (label: string, isDisable: boolean, callback: () => void, isActive: boolean = false): JSX.Element => {
        return (
            <button className={[
                isDisable ? 'disable' : '',
                isActive ? 'active' : ''
            ].join(' ')}
                onClick={callback}
            >
                <span>{label}</span>
            </button>
        );
    }

    const createInfoRecored = (label: string, innerJsx: JSX.Element): JSX.Element => {
        return (
            <div
                className="info-record"
            >
                <span>{label}</span>
                <span>{': '}</span>
                {innerJsx}
            </div>
        );
    }

    const isDisplayMsg = message != '';
    const selectedRecord = recordList[focus];
    return (
        <div id="scorecont-listtab-root">
            <div className="left-cont">
                {/* <button onClick={() => {
                    // dispatch(ActionType.SET_NOTE_DATA,
                    //     { data: { ...state.noteData, editMode: CHORD_MODE.SCALE } }
                    // );
                    dispatcher.setNoteData({ ...state.noteData, chordMode: CHORD_MODE.SCALE });
                    dispatch(ActionType.INIT_DEFAULT_ELEMENTS, null);
                    dispatch(ActionType.SELECT_NOTE_ELEMENT, {index: 2});
                    dispatch(ActionType.SELECT_SCORETAB, { index: 1 });
                }}
                >
                    <span>SCALE</span>
                </button>
                <button onClick={() => {
                    // dispatch(ActionType.SET_NOTE_DATA,
                    //     { data: { ...state.noteData, editMode: CHORD_MODE.DIRECT } }
                    // );
                    dispatcher.setNoteData({ ...state.noteData, chordMode: CHORD_MODE.DIRECT });
                    dispatch(ActionType.INIT_DEFAULT_ELEMENTS, null);
                    dispatch(ActionType.SELECT_NOTE_ELEMENT, {index: 2});
                    dispatch(ActionType.SELECT_SCORETAB, { index: 1 });
                }}
                >
                    <span>DIRECT</span>
                </button> */}
                <div className="opr">
                    {createButton('Create', isEdit || isLoad, () => {
                        setMessage('Create...');
                        sendQueryRequestToAPI('update', `INSERT INTO sclibtbl(user, title, chord_mode) VALUES(${state.login.id}, 'new_title', '')`)
                            .then((result) => {
                                reload(recordList.length);
                                return result;
                            })
                    })}
                    {createButton('Update', focus == -1 || isLoad, () => {
                        if (!isEdit) {
                            setEdit(true);
                            setTitle(selectedRecord.title);
                            setChordMode(selectedRecord.chord_mode);
                        }
                    }, isEdit)}
                    {createButton('Delete', focus == -1 || isEdit || isLoad, () => {
                        (async () => {
                            setMessage('Delete...');
                            console.log('----start----');
                            await sendQueryRequestToAPI('update', `delete from data_tbl where score = ${recordList[focus].id}`);
                            await sendQueryRequestToAPI('update', `delete from sclibtbl where id = ${recordList[focus].id}`);
                            reload(-1);
                            setFocus(-1);
                            console.log('----end----');
                        })();
                    })}
                </div>
                <div className={[
                    'lib',
                    isEdit ? 'disable' : ''
                ].join(' ')}>
                    <div className={[
                        'message',
                        !isDisplayMsg ? 'hidden' : ''
                    ].join(' ')}>
                        <span>{message}</span>
                    </div>
                    <div className={[
                        'list',
                        isDisplayMsg ? 'hidden' : ''
                    ].join(' ')}>
                        {titleList}
                    </div>
                </div>
            </div>
            <div className="right-cont">
                <div className="opr">
                    {createButton('Cancel', !isEdit || isLoad, () => {
                        setEdit(false);
                    })}
                    {createButton('Save', !isEdit || chordMode === '' || isLoad, () => {
                        setLoad(true);
                        (async () => {
                            console.log('----start----');
                            const values: string[] = [];
                            await sendQueryRequestToAPI('update', `update sclibtbl set title = '${title}', chord_mode = '${chordMode}' where id = ${selectedRecord.id}`);
                            if (state.noteData.dataList.length > 0) {
                                state.noteData.dataList.forEach((data, i) => {
                                    values.push(`(${selectedRecord.id}, ${i}, '${JSON.stringify(data)}')`);
                                });
                                await sendQueryRequestToAPI('update', `delete from data_tbl where score = ${selectedRecord.id}`);
                                await sendQueryRequestToAPI('update', `insert into data_tbl values${values.join(',')}`);
                            }
                            console.log('----end----');
                            alert('The data was saved successfully!');
                            // setEdit(false);
                            setLoad(false);
                        })();
                    })}
                    {createButton('Edit', !isEdit || chordMode === '' || isLoad, () => {
                        setLoad(true);
                        (async () => {
                            if (selectedRecord.data_cnt == 0) {
                                dispatch(ActionType.INIT_DEFAULT_ELEMENTS, null);
                            } else {
                                const response = await sendQueryRequestToAPI('select', `SELECT json from data_tbl where score = ${selectedRecord.id} order by seq`);
                                const results = await response.json();
                                // alert(JSON.stringify(results));
                                const dataList: NoteElement[] = [];
                                results.forEach((result: { json: string }) => {
                                    dataList.push(JSON.parse(result.json));
                                });
                                dispatcher.score.setNoteData({ ...state.noteData, dataList, chordMode: chordMode as CHORD_MODE  });
                            }
                            dispatch(ActionType.SELECT_NOTE_ELEMENT, { index: 2 });
                            dispatch(ActionType.SELECT_SCORETAB, { index: 1 });
                            setLoad(false);
                        })();
                    })}
                </div>
                {
                    !isEdit ? <></> : (
                        <div className="data">
                            {createInfoRecored('Id', (
                                <span className="value">{selectedRecord.id}</span>
                            ))}
                            {createInfoRecored('Title', (
                                <input
                                    type="text"
                                    onChange={(e) => {
                                        // recordList[focus].title = e.target.value;
                                        // setRecordList(recordList);
                                        setTitle(e.target.value);
                                    }}
                                    value={title}
                                />
                            ))}
                            {createInfoRecored('Mode', (
                                // <span className="value">{selectedRecord.chord_mode}</span>
                                <select className={[
                                    'value',
                                    selectedRecord.data_cnt > 0 ? 'disable' : ''
                                ].join(' ')}
                                    value={chordMode}
                                    onChange={(e) => {
                                        setChordMode(e.target.value as CHORD_MODE | '');
                                    }}>
                                    <option value=''></option>
                                    <option value={CHORD_MODE.SCALE}>{CHORD_MODE.SCALE}</option>
                                    <option value={CHORD_MODE.DIRECT}>{CHORD_MODE.DIRECT}</option>
                                </select>
                            ))}
                            {createInfoRecored('Show', (
                                <span className="value">{'PRIVATE'}</span>
                            ))}
                            {createInfoRecored('Data', (
                                <span className="value">{selectedRecord.data_cnt}</span>
                            ))}
                        </div>
                    )
                }
            </div>
        </div>
    );
}

export default ScoreContLiblaryTab;
