import { DEGREE12_FLAT_LIST, DEGREE12_SHARP_LIST, DIATONIC_MAJOR_SCALE_DEGREE_NAMES, KEY12_FLAT_LIST, KEY12_SHARP_LIST, SYMBOL_PARAMS_LIST } from 'contents/common/define';
import { GlobalContext } from 'contents/MainFrame';
import { ActionType } from 'contents/redux/reducer';
import { TypeNoteElement, DetailFixed, DetailSection, DetailInit, DetailChord, DetailChange, NoteElement, FloatWndState, Store, CHORD_MODE, EditMode } from 'contents/redux/store';
import React, { useContext, useEffect, useState, KeyboardEvent, useRef, MouseEvent } from 'react';
import ScoreElementDetail from './detail/ScoreElementDetail';
import './ScoreElement.css';

type ScoreElementProps = {
    index: number;
    noteElement: NoteElement;
}

// type ScoreElementProps = {
//     index: number;
//     type: TypeNoteElement;
//     progress: ProgressInfo;
// }
const ScoreElement = (props: ScoreElementProps) => {
    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    const isAccept = props.index < state.noteData.dataList.length;

    const ref = useRef({} as HTMLDivElement);
    useEffect(() => {
        if (isAccept) {
            dispatch(ActionType.GET_ELEMENT_REFFER, { pos: props.index, reffer: ref.current });
        }
    }, [state.noteData.dataList]);

    if (!isAccept) {
        return (<></>);
    }

    const type = props.noteElement.type;
    const editMode = state.noteState.editMode;
    // const isNormalMode = state.noteState.editMode == EditMode.NORMAL;
    const cache = state.noteState.cacheList[props.index];

    const getStyleFromType = (): string => {
        switch (type) {
            case TypeNoteElement.Fixed: return 'detail-fixed';
            case TypeNoteElement.Init: return 'detail-init';
            case TypeNoteElement.Chord: return 'detail-chord';
            case TypeNoteElement.Section: return 'detail-section';
            case TypeNoteElement.Change: return 'detail-change';
        }
    };

    const focusIndex = state.noteState.focusIndex;
    const isSelected = focusIndex == props.index;
    const isCriteria = state.noteState.criteriaIndex == props.index;
    /**
     * 
     * @returns 
     */
    const isCurrentSection = (): boolean => {
        const dataList = state.noteData.dataList;
        const posList: number[] = [dataList.length];
        let secNo = -1;
        let curSecNo = -1;
        dataList.forEach((el, i) => {
            if (el.type == TypeNoteElement.Section) secNo++;
            posList[i] = secNo;
            if (focusIndex == i) curSecNo = secNo;
        });
        // const isChordEl = dataList[props.index].type == TypeNoteElement.Chord;
        const isCurrentSec = posList[props.index] == curSecNo && type != TypeNoteElement.Fixed;
        return isCurrentSec;
    }

    /**
 * 
 * @returns 
 */
    const getCopyTargetStyle = (): string => {
        if (editMode != EditMode.BACKING) return '';

        if (type == TypeNoteElement.Chord) {
            const detail = props.noteElement.detail as DetailChord;
            const isSameStructType = detail.molecule != null;
            if (isSameStructType) {
                if (state.noteState.selectedList.includes(props.index)) {
                    return 'copy-selected';
                }
                return 'copy-tgt';
            } else {
                return 'copy-no-tgt';
            }
        } else {
            return 'copy-no-tgt';
        }
    }

    const getHeaderElement = (): JSX.Element => {
        let retJsxList: JSX.Element[] = [];

        if ([TypeNoteElement.Chord, TypeNoteElement.Section, TypeNoteElement.Change].includes(type)) {

            if (TypeNoteElement.Chord == type) {

                retJsxList.push(
                    <div key={retJsxList.length} className="chord-no">
                        <span>{cache.progress.no}</span>
                    </div>
                );
            }
            retJsxList.push(
                //  <div className="edit">
                //         <span>E</span>
                //     </div> 
                <div
                    key={retJsxList.length}
                    className={
                        'del' +
                        (editMode == EditMode.NORMAL ? '' : ' hidden')
                    }
                    onClick={() => {
                        dispatcher.score.deleteElement(props.index);
                    }}
                >
                    <span></span>
                </div >
            );
        }
        return (<>
            {retJsxList}
        </>);
    }

    /**
     * 
     */
    const normalElementClickAction = () => {
        const element: NoteElement = state.noteData.dataList[props.index];
        if (element.type != TypeNoteElement.Fixed) {
            dispatch(ActionType.SELECT_NOTE_ELEMENT, { index: props.index });
        }
    }
    /**
     * 
     */
    const selectorElementClickAction = () => {
        const selectedList = state.noteState.selectedList;
        if (!selectedList.includes(props.index)) {
            selectedList.push(props.index);
        } else {
            selectedList.some((index, i) => {
                if (index == props.index) selectedList.splice(i, 1);
            });
        }
        dispatcher.score.setSelectedList(selectedList);
    }

    // 
    let additionBtn: JSX.Element = <></>;
    if (props.index == state.noteData.dataList.length - 2) {
        additionBtn = (
            <div
                className={
                    'addition' +
                    (editMode == EditMode.NORMAL ? '' : ' disable')
                }
                onClick={() => {
                    dispatch(ActionType.SELECT_NOTE_ELEMENT, { index: props.index });
                    dispatcher.score.addChordElement();
                    dispatch(ActionType.SELECT_NOTE_ELEMENT, { index: props.index + 1 });
                    dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CHORD_PALETTE });
                    dispatch(ActionType.CHORD_PALETTE_SETTER, {
                        params: {
                            ...state.noteState.chordPaletteState,
                            isAddition: true,
                            rootIndex: 0,
                            isFlat: false,
                            symbolKey: SYMBOL_PARAMS_LIST[0].key,
                            sustain: 4,
                        }
                    });
                }}>
                <span>+</span>
            </div>
        );
    }

    return (
        <div className="box">
            <div className="info-base">
                <div className="header">
                    {getHeaderElement()}
                </div>
                <div
                    className={[
                        'note-element',
                        getStyleFromType(),
                        isSelected ? 'active' : '',
                        isCriteria ? 'criteria' : '',
                        [
                            TypeNoteElement.Init,
                            TypeNoteElement.Section,
                            TypeNoteElement.Chord,
                            TypeNoteElement.Change,
                        ].includes(type) ? 'selectable' : '',
                        isCurrentSection() ? 'current' : '',
                        getCopyTargetStyle()
                    ].join(' ')}
                    onClick={() => {
                        if (editMode == EditMode.NORMAL) {
                            normalElementClickAction();
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                        } else {
                            selectorElementClickAction();
                        }
                    }}
                    onContextMenu={(e: MouseEvent) => {
                        if (editMode == EditMode.NORMAL) {
                            normalElementClickAction();
                            const element: NoteElement = state.noteData.dataList[props.index];
                            switch (element.type) {
                                case TypeNoteElement.Init:
                                    {
                                        dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.INIT_PALETTE });
                                    }
                                    break;
                                case TypeNoteElement.Section:
                                case TypeNoteElement.Chord:
                                case TypeNoteElement.Change:
                                    {
                                        state.reffers.contextMneu.style.left = e.clientX + 'px';
                                        state.reffers.contextMneu.style.top = e.clientY + 'px';
                                        dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CONTEXT_MENU });
                                    }
                                    break;
                            }
                        }
                    }}
                    ref={ref}
                >
                    <ScoreElementDetail
                        index={props.index}
                        noteElement={props.noteElement}
                    />
                </div >
            </div >
            {/*  */}
            {additionBtn}
        </div >
    );

};

export default ScoreElement;
