import { KEY12_MAJOR_SCALE_LIST, KEY12_MINOR_SCALE_LIST, RYTHM_LIST, SCALE_LIST } from "contents/common/define";
import { GlobalContext } from "contents/MainFrame";
import { ActionType } from "contents/redux/reducer";
import { CHORD_MODE, FloatWndState } from "contents/redux/store";
import { useContext, useEffect, useRef, useState } from "react";
import styled from "styled-components";

import './InitEditor.css';

const InitEditor = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    const [tempo, setTempo] = useState(55);
    const [rythm, setRythm] = useState('4/4');
    const [keyIndex, setKey] = useState(0);
    const [scale, setScale] = useState('major');

    const ref = useRef({} as HTMLDivElement);
    useEffect(() => {
        // dispatch(ActionType.GET_REFFER, { reffers: {...state.reffers, chordPalette: ref.current }});
        dispatch(ActionType.GET_REFFER_CHORD_PALETTE, { reffer: ref.current });
    }, []);

    let operation: JSX.Element = <></>;
    operation = (<>
        <div
            onClick={() => {
                dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            }}>
            <span>Cancel</span>
        </div>
        <div
            onClick={() => {
                dispatcher.init.setInitParam(tempo, rythm, keyIndex, scale);
            }}>
            <span>Enter</span>
        </div>
    </>);



    const getItemSlider = (label: string, list: string[]): JSX.Element => {
        return (
            <div className="item">
                <div className="label">
                    <span>{label}</span>
                </div>
                <div className="value">
                    {/* <span>{tempo}</span> */}
                    <input
                        type="number" value={tempo} min="30" max="180"
                        onChange={(e) => {
                            setTempo(Number(e.target.value));
                        }}
                    />
                    <input
                        type="range" name="speed" min="30" max="180" value={tempo}
                        onChange={(e) => {
                            setTempo(Number(e.target.value));
                        }}
                    />
                </div>
            </div>
        );
    }
    const getItemSelector = (label: string, value: string, callback: (value: string) => void,
        list: { value: string, display: string }[]): JSX.Element => {
        const optionsJsx: JSX.Element[] = [];
        list.forEach((data) => {
            optionsJsx.push(
                <option key={optionsJsx.length} value={data.value}>{data.display}</option>
            );
        });
        return (
            <div className="item">
                <div className="label">
                    <span>{label}</span>
                </div>
                <div className="value">
                    <select
                        value={value}
                        onChange={(e) => {
                            callback(e.target.value);
                        }}
                    >
                        {optionsJsx}
                    </select>
                </div>
            </div>
        );
    }
    const isDirect = state.noteData.chordMode == CHORD_MODE.DIRECT;
    return (
        <div
            id="init-editor"
            className={'floatwnd' + (state.noteState.floatwndState == FloatWndState.INIT_PALETTE ? ' active' : '')}
            onClick={() => {
            }}
        >
            <div className="items">
                {getItemSlider('Tempo', [])}
                {getItemSelector('Beat', rythm, (value: string) => { setRythm(value) },
                    RYTHM_LIST.map(item => { return { value: item, display: item } })
                )}
                {isDirect ? <></> :
                    getItemSelector('Key', String(keyIndex), (value: string) => { setKey(Number(value)) },
                        (scale == SCALE_LIST[0] ?
                            // 
                            KEY12_MAJOR_SCALE_LIST.map((item, i) => { return { value: i.toString(), display: item } })
                            :
                            // 
                            KEY12_MINOR_SCALE_LIST.map((item, i) => { return { value: i.toString(), display: item } })
                        )
                    )
                }
                {isDirect ? <></> :
                    getItemSelector('Scale', scale, (value: string) => { setScale(value) },
                        SCALE_LIST.map(item => { return { value: item, display: item } })
                    )
                }
            </div>
            <div className="operation">
                {operation}
            </div>
        </div>
    );
}

export default InitEditor;

const StyledButton = styled.span`
  user-select: none;
  cursor: pointer;
  display: inline-block;
  margin: 1em;
  padding: 0.3em 0.5em;
  border-radius: 0.2em;
  border: solid 1px #3498db;
  color: #fff;
  background-color: #3498db;
`