import { GlobalContext } from 'contents/MainFrame';
import { ActionType } from 'contents/redux/reducer';
import { useContext } from 'react';
import './UserPlate.css';

const UserPlate = (
    props: {
    }
) => {
    const { state, dispatch, dispatcher } = useContext(GlobalContext);
    return (
        <div id="user-plate" className={[''].join(' ')}>
            <div className="user-name">
                <span>{state.login.name}</span>
            </div>
            <div
                className="logout"

                onClick={() => {
                    dispatcher.score.setNoteData({
                        ...state.noteData,
                        dataList: []
                    });
                    dispatcher.setLoginParams({
                        ...state.login,
                        isLogin: false,
                        name: ''
                    });
                    dispatch(ActionType.SELECT_SCORETAB, { index: 0 });
                }}>
                <span>LOGOUT</span>
            </div>
        </div>
    );
}

export default UserPlate;