
import { GlobalContext } from 'contents/MainFrame';
import { ActionType } from 'contents/redux/reducer';
import { FloatWndState } from 'contents/redux/store';
import { useContext, useEffect, useRef, useState } from 'react';
import './SectionEditor.css';

const SectionEditor = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);


    const ref = useRef({} as HTMLDivElement);
    useEffect(() => {
        // dispatch(ActionType.GET_REFFER, { reffers: {...state.reffers, chordPalette: ref.current }});
        dispatch(ActionType.GET_REFFER_SECTION_PALETTE, { reffer: ref.current });
    }, []);

    const [labelName, setLabelName] = useState('Section');

    let operation: JSX.Element = <></>;
    operation = (<>
        <div
            onClick={() => {
                dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            }}>
            <span>Cancel</span>
        </div>
        <div
            onClick={() => {
                dispatcher.section.setSectionParam(labelName);
            }}>
            <span>Enter</span>
        </div>
    </>);

    const getTextForm = (label: string, value: string, callback: (value: string) => void): JSX.Element => {
        return (
            <div className="item">
                <div className="label">
                    <span>{label}</span>
                </div>
                <div className="value">
                    <input
                        type="text"
                        value={value}
                        onChange={(e) => {
                            callback(e.target.value);
                        }}
                    />
                </div>
            </div>
        );
    }

    return (

        <div
            id="section-editor"
            className={'floatwnd' + (state.noteState.floatwndState == FloatWndState.SECTION_PALETTE ? ' active' : '')}
            onClick={() => {
            }}
        >
            <div className="items">
                {getTextForm('Name', labelName, (value: string) => { setLabelName(value) })}
            </div>
            <div className="operation">
                {operation}
            </div>
        </div>
    );
}

export default SectionEditor;