export const getChangeTypeLabel = (changeType: ChangeType) => {
    switch(changeType) {
        case ChangeType.MODULATE: return 'MODULATE';
        case ChangeType.TEMPO: return 'TEMPO';
        case ChangeType.BEAT: return 'BEAT';
    }
}

