import { Key12Info } from "contents/redux/store";
import { API_DOMAIN, KEY12_FLAT_LIST, KEY12_SHARP_LIST, SymbolKey, SymbolParams, SYMBOL_PARAMS_LIST } from "./define";

export function getSymbolParams(key: SymbolKey): SymbolParams | null {
    let symbol = null;
    SYMBOL_PARAMS_LIST.forEach((data) => {
        if (data.key == key) {
            symbol = data;
        }
    });
    return symbol;
}

export const getAdjustSusValue = (value: number): string => {
    let ret = '';
    if (value != 0) {
        if (value > 0) ret = '+' + value;
        else ret = value.toString();
    }
    return ret;
}

/** */
export const getChordRoot = (rootInfo: Key12Info | null, keyIndex: number): string | null => {
    let name = null;
    if (rootInfo != null) {
        const list = rootInfo.isFlat ? KEY12_FLAT_LIST : KEY12_SHARP_LIST;
        name = list[(rootInfo.rootIndex + keyIndex) % 12];
    }
    return name;
}

/** */
export const getSymbolName = (symbolKey: SymbolKey): string => {
    let name = '';
    SYMBOL_PARAMS_LIST.forEach((symbol) => {
        if (symbol.key == symbolKey) {
            name = symbol.name;
        }
    });
    return name;
}

export const createQueryRequestInit = (sql: string): RequestInit => {
    return {
        mode: 'cors',
        method: 'POST',
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({ sql })
    }
}

export const sendQueryRequestToAPI = (queryType: 'select' | 'update', sql: string): Promise<Response> => {
    return fetch(API_DOMAIN + '/' + queryType,
        createQueryRequestInit(sql)
    );
}

export const applyOnChord = (symbol: SymbolParams, molRootIndex: number, denRootIndex: number) => {

    // 
    let denInterval = denRootIndex - molRootIndex;
    if (denInterval < 0) {
        denInterval += 12;
    }
    const fixedIntervals: number[] = [];
    const fixedStruct: string[] = [];
    let isAdded = false;
    symbol.intervals.forEach((cur, i) => {
        if (i < symbol.intervals.length) {
            // const next = symbol.intervals[i + 1];
            // if(cur <= denInterval && denInterval < next) {
            if (!isAdded && denInterval < cur) {
                fixedIntervals.push(denInterval);
                fixedStruct.push('den');
                isAdded = true;
            }
            if (denInterval != cur) {
                fixedIntervals.push(cur);
                fixedStruct.push(symbol.struct[i]);
            }
        }
    });
    if (!isAdded) {
        fixedIntervals.push(denInterval);
        fixedStruct.push('den');
    }
    symbol.intervals = fixedIntervals;
    symbol.struct = fixedStruct;

}