
import { KEY12_SHARP_LIST, SymbolParams, SYMBOL_PARAMS_LIST } from "contents/common/define";
import { applyOnChord, getChordRoot, getSymbolParams } from "contents/common/utils";
import { GlobalContext } from "contents/MainFrame";
import { ActionType } from "contents/redux/reducer";
import { DetailChord, FloatWndState, CHORD_MODE, BACKING_LEN_TYPE, BackingLen, BACKING_SIDE, BackingEditorState, BackingLayer, ChannelParam, Key12Info } from "contents/redux/store";
import { createContext, useContext, useEffect, useRef, useState } from "react";

import './BackingEditor.css';
import BackingTable, { ChannelBean } from "./BackingTable";
import StructMatrix from "./StructMatrix";

type BackingContextType = {
    // isCorrect: boolean;
    // setCorrect: (isCorrect: boolean) => void;
    editorState: BackingEditorState;
}

export const BackingContext = createContext({} as BackingContextType);

const BackingEditor = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    // const [isCorrect, setCorrect] = useState(true);

    /**  */
    const ref = useRef({} as HTMLDivElement);
    useEffect(() => {
        // dispatch(ActionType.GET_REFFER, { reffers: {...state.reffers, chordPalette: ref.current }});
        dispatch(ActionType.GET_REFFER_BACKING_PALETTE, { reffer: ref.current });
    }, []);

    // useEffect(() => {
    //     dispatcher.backing.setCorrect(isCorrect);
    // }, [isCorrect]);
    // if (state.noteState.focusIndex == -1 ||
    //     state.noteData.dataList[state.noteState.focusIndex].type != TypeNoteElement.Chord) {
    //     return (<></>);
    // }
    if (state.noteState.floatwndState != FloatWndState.BACKING_PALETTE) {
        return (<></>);
    }

    const paletteState = state.noteState.backingPaletteState as BackingEditorState;
    
    // const foocusIndex = state.noteState.focusIndex;
    // const detail = state.noteData.dataList[foocusIndex].detail as DetailChord;
    // const cache = state.noteState.cacheList[foocusIndex];
    // if(detail.symbolKey == null) return (<></>);
    // let rootIndex = (detail.molecule as Key12Info).rootIndex;
    // const symbol: SymbolParams = { ...(getSymbolParams(detail.symbolKey) as SymbolParams) };

    const molecule = paletteState.molecule as Key12Info;
    const denominator = paletteState.denominator;
    const symbol: SymbolParams = { ...(getSymbolParams(paletteState.symbolKey) as SymbolParams) };
    const sustain = paletteState.sustain;
    const headAdjust = paletteState.headAdjust;
    const tailAdjust = paletteState.tailAdjust;

    const root = KEY12_SHARP_LIST[molecule.rootIndex];
    let chordName = root + symbol.name;
    const structs: string[] = [];
    //
    if (denominator != null) {

        // 
        chordName += `/${getChordRoot(denominator, 0)}`;
        
        // 
        applyOnChord(symbol, molecule.rootIndex, denominator.rootIndex);
    }
    symbol.intervals.forEach((value) => {
        // structs.push(KEY12_SHARP_LIST[(cache.init.keyIndex + rootIndex + value) % 12]);
        structs.push(KEY12_SHARP_LIST[(molecule.rootIndex + value) % 12]);
    });
    
    const channelList = getChannelList(paletteState.useChannels, molecule.rootIndex, symbol, structs);

    const testPlaySound = (soundFullName: string) => {
        state.sampler.triggerAttack(soundFullName, '+0', 0.15);
        setTimeout(() => {
            state.sampler.triggerRelease(soundFullName);
        }, 1000);
    };

    let operation: JSX.Element = <></>;
    operation = (<>
        <div
            onClick={() => {
                dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            }}>
            <span>Cancel</span>
        </div>
        <div
            className={[
                paletteState.isCorrect ? '' : 'disable'
            ].join(' ')}
            onClick={() => {
                dispatcher.backing.play(false);
            }}>
            <span>Play</span>
        </div>
        <div
            className={[
                paletteState.isCorrect ? '' : 'disable'
            ].join(' ')}
            onClick={() => {
                dispatcher.backing.setCurBackingScript();
            }}>
            <span>Update</span>
        </div>
    </>);
    return (
        <BackingContext.Provider value={{
            editorState: paletteState
            // isCorrect: isCorrect,
            // setCorrect: (isCorrect) => setCorrect(isCorrect)
        }}>
            <div
                ref={ref}
                id="backing-palette"
                className={'floatwnd' + (state.noteState.floatwndState == FloatWndState.BACKING_PALETTE ? ' active' : '')}
                onClick={() => {
                }}
            >
                <div className="base-info">
                    <div className="record">
                        <div className="title"><span>Chord:</span></div>
                        <div className="value"><span>{chordName}</span></div>
                    </div>
                    <div className="record">
                        <div className="title"><span>Category:</span></div>
                        <div className="value"><span>{symbol.category}</span></div>
                    </div>
                    <div className="record">
                        <div className="title"><span>Struct:</span></div>
                        <div className="value"><span>{structs.join(', ')}</span></div>
                    </div>
                    <div className="record">
                        <div className="title"><span>Sustain:</span></div>
                        <div className="value"><span>{sustain + headAdjust + tailAdjust}</span></div>
                    </div>
                </div>
                <StructMatrix
                    paletteState={paletteState}
                    structs={structs}
                    symbol={symbol}
                    degreeIndex={molecule.rootIndex}
                    testPlaySound={testPlaySound}
                    setUseChannels={(useChannels: ChannelParam[]) => dispatcher.setUseChannels(useChannels)}
                />
                <BackingTable
                    channelList={channelList}
                    paletteState={paletteState}
                    dispatcher={dispatcher}
                    testPlaySound={testPlaySound}
                />

                <div className="operation">
                    {operation}
                </div>
            </div>
        </BackingContext.Provider>
    );
}

const getChannelList = (useChannels: ChannelParam[], degreeIndex: number, symbol: SymbolParams, structs: string[]): ChannelBean[] => {

    const channelList: ChannelBean[] = [];

    const OctaveMax = 8;
    for (let i = 0; i < symbol.intervals.length; i++) {
        // for (let i = ChordStructMax - 1; i >= 0; i--) {
        for (let j = 0; j < OctaveMax; j++) {
            // for (let j = OctaveMax - 1; j >= 0; j--) {
            const soundIndex = degreeIndex + symbol.intervals[i] + j * 12;
            let isSelected = false;
            useChannels.forEach((channel) => {
                if (channel.octave == j && channel.structIndex == i) {
                    isSelected = true;
                }
            });
            const octave = Math.floor(soundIndex / 12);
            const soundFullName = structs[i] + octave;
            if (isSelected) {
                channelList.push({ soundIndex, soundFullName });
            }
        }
    }
    // 
    channelList.sort((a, b) => {
        return b.soundIndex - a.soundIndex;
    });
    return channelList;
}

export default BackingEditor;