import { GlobalContext } from 'contents/MainFrame';
import { FloatWndState } from 'contents/redux/store';
import { useContext } from 'react';
import './SettingEditor.css';

const SettingEditor = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);


    if (state.noteState.floatwndState != FloatWndState.SETTING_EDITOR) {
        return (<></>);
    }
    const defsus = state.noteState.defaultSustain;
    const sustainJsxList: JSX.Element[] = [];
    for (let i = 0; i < 8; i++) {
        sustainJsxList.push(
            <div
                key={sustainJsxList.length}
                className={[
                    i + 1 <= defsus ? 'active' : ''
                ].join(' ')}
                onClick={()=>{
                    dispatcher.score.setNoteState({...state.noteState, defaultSustain: i + 1});
                }}
            />
        );
    }
    return (
        <div id="setting-editor"
            className="floatwnd active">
            <div className="sustain">{sustainJsxList}</div>
        </div>
    );
}

export default SettingEditor;
