import { Dispatcher } from "contents/redux/dispatcher";
import { BackingLen, BackingEditorState, BACKING_LEN_TYPE, BACKING_SIDE } from "contents/redux/store";


type BackingOperationProps = {
    dispatcher: Dispatcher;
    paletteState: BackingEditorState;
}

type ButtonBean = {
    label: string;
    isActive: boolean;
    isDisable: boolean;
    callback: Function;
}

const BackingOperation = (props: BackingOperationProps) => {

    const dispatcher = props.dispatcher;
    const paletteState = props.paletteState;
    const sideIndex = paletteState.sideIndex;
    const curSideScript = paletteState.layers[sideIndex];
    const selectedLenIndex = paletteState.selectedLenIndex;
    const curLenBean = curSideScript.lens[selectedLenIndex];
    const curVolume = curSideScript.vels[selectedLenIndex];

    const setCurrentLenBean = (lenBean: BackingLen) => {
        const script = paletteState.layers;
        script[sideIndex].lens[selectedLenIndex] = lenBean;
        dispatcher.initBackingPalette({
            ...paletteState,
            layers: script
        });
    };
    const setCurrentLen = (lenType: BACKING_LEN_TYPE) => {
        curLenBean.len = lenType;
        curLenBean.isHalf = false;
        curLenBean.isDiv3 = false;
        setCurrentLenBean(curLenBean);
    };
    const updateCurrentVolume = (sub: number) => {
        const script = paletteState.layers;
        const nowVol = script[sideIndex].vels[selectedLenIndex];
        script[sideIndex].vels[selectedLenIndex] = nowVol + sub;
        dispatcher.initBackingPalette({
            ...paletteState,
            layers: script
        });
    };

    const curLen = curLenBean.len;
    const buttons: ButtonBean[] = [
        {
            label: '16',
            isActive: curLen == BACKING_LEN_TYPE.LEN16,
            isDisable: false,
            callback: () => { setCurrentLen(BACKING_LEN_TYPE.LEN16); }
        },
        {
            label: '8',
            isActive: curLen == BACKING_LEN_TYPE.LEN8,
            isDisable: false,
            callback: () => { setCurrentLen(BACKING_LEN_TYPE.LEN8); }
        },
        {
            label: '4',
            isActive: curLen == BACKING_LEN_TYPE.LEN4,
            isDisable: false,
            callback: () => { setCurrentLen(BACKING_LEN_TYPE.LEN4); }
        },
        {
            label: '2',
            isActive: curLen == BACKING_LEN_TYPE.LEN2,
            isDisable: false,
            callback: () => { setCurrentLen(BACKING_LEN_TYPE.LEN2); }
        },
        {
            label: '1',
            isActive: curLen == BACKING_LEN_TYPE.LEN1,
            isDisable: false,
            callback: () => { setCurrentLen(BACKING_LEN_TYPE.LEN1); }
        },
        {
            label: '.',
            isActive: curLenBean.isHalf,
            isDisable: !dispatcher.backing.isAcceptHalf(),
            callback: () => {
                curLenBean.isHalf = !curLenBean.isHalf;
                curLenBean.isDiv3 = false;
                setCurrentLenBean(curLenBean);
            }
        },
        {
            label: '/ 3',
            isActive: curLenBean.isDiv3,
            isDisable: !dispatcher.backing.isAcceptDiv3(),
            callback: () => {
                curLenBean.isDiv3 = !curLenBean.isDiv3;
                curLenBean.isHalf = false;
                setCurrentLenBean(curLenBean);
            }
        },
        {
            label: 'Add',
            isActive: false,
            isDisable: false,
            callback: () => {
                dispatcher.backing.addLen();
            }
        },
        {
            label: 'Del',
            isActive: false,
            isDisable: curSideScript.lens.length <= 1,
            callback: () => {
                dispatcher.backing.delLen();
            }
        },
        {
            label: 'vel-',
            isActive: false,
            isDisable: curVolume <= 1,
            callback: () => {
                updateCurrentVolume(-1);
            }
        },
        {
            label: 'vel+',
            isActive: false,
            isDisable: curVolume >= 10,
            callback: () => {
                updateCurrentVolume(1);
            }
        },
        // {
        //     label: 'all clear',
        //     isActive: false,
        //     isDisable: false,
        //     callback: () => {
        //         const backingState: BackingEditorState = {
        //             useChannels: [],
        //             isUsePedal: false,
        //             sideIndex: 0,
        //             selectedLenIndex: 0,
        //             selectedChannelIndex: -1,
        //             layers: [
        //                 {
        //                     lens: [{
        //                         len: BACKING_LEN_TYPE.LEN4,
        //                         isDiv3: false,
        //                         isHalf: false
        //                     }],
        //                     side: BACKING_SIDE.LEFT,
        //                     table: [],
        //                     vels: [5]
        //                 },
        //                 {
        //                     lens: [{
        //                         len: BACKING_LEN_TYPE.LEN4,
        //                         isDiv3: false,
        //                         isHalf: false
        //                     }],
        //                     side: BACKING_SIDE.RIGHT,
        //                     table: [],
        //                     vels: [5]
        //                 }
        //             ],
        //             isCorrect: true
        //         };
        //         dispatcher.initBackingPalette(backingState);
        //     }
        // }
    ];
    const buttonList: JSX.Element[] = [];
    buttons.forEach((button, i) => {
        buttonList.push(
            <button
                key={i}
                className={
                    (button.isActive ? 'active' : '') +
                    (button.isDisable ? ' disable' : '')
                }
                onClick={() => button.callback()}
            >
                <span>{button.label}</span>
            </button>
        );
    });

    return (<>
        <select
            value={paletteState.channelRowSize}
            onChange={(e) => {
                const backing = props.paletteState;
                const newValue = parseInt(e.target.value);
                const diff = newValue - backing.channelRowSize;
                const layers = backing.layers;
                if (diff > 0) {
                    layers.forEach((layer) => {
                        for (let i = 0; i < diff; i++) {
                            const record: boolean[][] = [];
                            for (let j = 0; j < layer.lens.length; j++) {
                                record.push([false]);
                            }
                            layer.table.splice(backing.channelRowSize, 0, record);
                        }
                    });
                } else if (diff < 0) {
                    layers.forEach((layer) => {
                        layer.table.splice(newValue, Math.abs(diff));
                    });
                }
                backing.channelRowSize = newValue;
                dispatcher.backing.setPaletteState(props.paletteState);
            }}>
            {(() => {
                const list: JSX.Element[] = [];
                for (let i = 0; i < 12; i++) {
                    const val = i + 1;
                    const isDisabled = val < paletteState.channelRowSize;
                    list.push(
                        <option
                            key={i}
                            value={val}
                            className={isDisabled ? 'del' : ''}
                        >
                            {val}
                        </option>
                    );
                }
                return list;
            })()}
        </select>
        {buttonList}
    </>);
}

export default BackingOperation;