import { SymbolKey, SYMBOL_PARAMS_LIST, TimeSignatures } from 'contents/common/define'
import { Sampler } from 'tone'
import { ActionType } from './reducer'

export type Store = {
    login: LoginParams;
    scoreTabIndex: number;
    noteData: NoteData;
    noteState: NoteState;
    reffers: Reffers;
    sampler: Sampler;
    soundUseList: number[];
}

export type LoginParams = {
    isLogin: boolean,
    id: number,
    name: string
}

export type ScoreCache = {
    reffer?: HTMLDivElement;
    init: DetailInit;
    progress: ScoreProgress;
}

/**
 * 
 */
 export type ScoreProgress = {
    /**  */
    no: number;
    /**  */
    barCount: number;
    /**  */
    remaindBeat: number;
    /**  */
    headMSec: number;
}

export type Reffers = {
    contextMneu: HTMLDivElement;
    chordPalette: HTMLDivElement;
    sectionPalette: HTMLDivElement;
    changePalette: HTMLDivElement;
    initPalette: HTMLDivElement;
    backingPalette: HTMLDivElement;
}

export enum FloatWndState {
    NONE,
    CONTEXT_MENU,
    CHORD_PALETTE,
    SECTION_PALETTE,
    CHANGE_PALETTE,
    INIT_PALETTE,
    BACKING_PALETTE,
    SETTING_EDITOR,
}
export type ChordPaletteState = {
    isAddition: boolean;
    selectedLine: ChordPaletteLine,
    // rootIndex: number;
    // isFlat: false;
    molecule: Key12Info;
    denominator: Key12Info | null;
    symbolKey: SymbolKey;
    sustain: number;
    headAdjust: number;
    tailAdjust: number;
}
export enum ChordPaletteLine {
    Root, Symbol, Sustain, Adjuster
}

export type BackingEditorState = {
    molecule: Key12Info;
    denominator: Key12Info | null;
    symbolKey: SymbolKey;
    sustain: number;
    headAdjust: number;
    tailAdjust: number;
    channelRowSize: number;
    useChannels: ChannelParam[];
    layers: BackingLayer[];
    isUsePedal: boolean;
    selectedLenIndex: number;
    sideIndex: number;
    selectedChannelIndex: number;
    isCorrect: boolean;
}
export type ChannelParam = {
    octave: number;
    structIndex: number;
}
export type BackingLayer = {
    side: BACKING_SIDE;
    lens: BackingLen[];
    table: boolean[][][];
    vels: number[];
}
export enum BACKING_SIDE {
    LEFT = 'LEFT',
    RIGHT = 'RIGHT'
}

export type BackingLen = {
    len: BACKING_LEN_TYPE;
    isHalf: boolean;
    isDiv3: boolean;
}

export enum BACKING_LEN_TYPE {
    LEN16 = 'LEN16',
    LEN8 = 'LEN8',
    LEN4 = 'LEN4',
    LEN2 = 'LEN2',
    LEN1 = 'LEN1',
}


export type NoteData = {
    scoreName: string;
    chordMode: CHORD_MODE;
    dataList: NoteElement[];
}

/**
 * 
 */
export enum CHORD_MODE {
    /**  */
    SCALE = 'SCALE',
    /**  */
    DIRECT = 'DIRECT'
}

export type NoteState = {
    focusIndex: number;
    cacheList: ScoreCache[];
    criteriaIndex: number;
    editMode: EditMode;
    selectedList: number[];
    defaultSustain: number;
    floatwndState: FloatWndState;
    chordPaletteState: ChordPaletteState;
    backingPaletteState: null | BackingEditorState;
    backingThreads: NodeJS.Timeout[];
}

export enum EditMode {
    NORMAL,
    BACKING,
    PREVIEW,
}

export type DetailInit = {
    tempo: number;
    rythm: string;
    keyIndex: number;
    scale: string;
}

export type DetailSection = {
    labelName: string;
}

export type DetailChord = {
    molecule: Key12Info | null;
    denominator: Key12Info | null;
    symbolKey: SymbolKey;
    sustain: number;
    headAdjust: number;
    tailAdjust: number;
    backing: BackingScript;
}

export type Key12Info = {
    rootIndex: number;
    isFlat: boolean;
}

export type BackingScript = {
    useChannels: ChannelParam[];
    layers: BackingLayer[];
    isUsePedal: boolean;
}

export type DetailFixed = {
    label: string;
}

export type DetailChange = {
    changeType: null | ChangeType;
    modulateRelation: null | ModulateRelation;
    domminantValue: number;
    keyAdjust: number;
    tempoRelation: null | TempoRelation;
    tempoDiff: number;
    tempoRate: number;
    tempoAbsolute: number;
    beat: null | TimeSignatures;
}

export enum ChangeType {
    MODULATE = 'Modulate',
    TEMPO = 'Tempo',
    BEAT = 'Beat'
}

// export type ChangeModulation = {
//     relation: ModulateRelation;
//     value: number;
// }

export enum ModulateRelation {
    DOMINANT = 'Domminant',
    PARALLEL = 'Parallel',
    RELATIVE = 'Relative',
    KEY = 'Key',
}

// export type ChangeBeat = {
//     value: number;
// }

// export type ChangeTempo = {
//     relation: TempoRelation;
//     value: number;
// }

export enum TempoRelation {
    DIFF = 'Diff',
    RATE = 'Rate',
    ABSOLUTE = 'Absolute'
}

export type NoteElement = {
    type: TypeNoteElement;
    detail: DetailInit | DetailSection | DetailChord | DetailChange | DetailFixed;
}

/**
 * 
 */
export enum TypeNoteElement {
    /**  */
    Init = 'init',
    /**  */
    Section = 'section',
    /**  */
    Chord = 'chord',
    /**  */
    Change = 'Change',
    /**  */
    Fixed = 'fixed'
}

/**
 * 
 */
export type Action = {
    type: ActionType;
    payload: any;
}

/**
 * 
 */
export const initialState: Store = {
    login: {
        isLogin: false,
        id: -1,
        name: ''
    },
    scoreTabIndex: 0,
    noteData: { scoreName: '', chordMode: CHORD_MODE.SCALE, dataList: [] },
    noteState: {
        focusIndex: -1,
        cacheList: [],
        criteriaIndex: -1,
        editMode: EditMode.NORMAL,
        selectedList: [],
        defaultSustain: 2,
        floatwndState: FloatWndState.NONE,
        chordPaletteState: {
            isAddition: false,
            selectedLine: ChordPaletteLine.Root,
            // rootIndex: 0,
            // isFlat: false,
            molecule: {rootIndex: 0, isFlat: false},
            denominator: null,
            symbolKey: SYMBOL_PARAMS_LIST[0].key,
            sustain: 4,
            headAdjust: 0,
            tailAdjust: 0
        },
        backingPaletteState: null,
        // {
        //     channelRowSize: 1,
        //     useChannels: [],
        //     isUsePedal: false,
        //     sideIndex: 0,
        //     selectedLenIndex: 0,
        //     selectedChannelIndex: -1,
        //     layers: [],
        //     isCorrect: true
        // },
        backingThreads: []
    },
    reffers: {} as Reffers,
    sampler: initSampler(),
    soundUseList: new Array(88)
};

function initSampler() {
    return new Sampler({
        "E2": "https://dl.dropbox.com/s/0aghs80w9aggubw/KEPSREC040.mp3?dl=0",
        "E3": "https://dl.dropbox.com/s/wxtf0u0ly03hu44/KEPSREC052.mp3?dl=0",
        "A#3": "https://dl.dropbox.com/s/hbm9d1vo5tlxymk/KEPSREC058.mp3?dl=0",
        "E4": "https://dl.dropbox.com/s/3qy88udfkzmzr13/KEPSREC064.mp3?dl=0",
        "E5": "https://dl.dropbox.com/s/tdjp24r9pfoi2ff/KEPSREC076.mp3?dl=0",
        "E6": "https://dl.dropbox.com/s/onp7h2vn2d6nhs7/KEPSREC088.mp3?dl=0",
    }).toDestination();
}