import './ScoreContSourceTab.css';

// import TabbedPane from './common/TabbedPane.jsx';
import { DetailSection, CHORD_MODE, Store, TypeNoteElement, EditMode } from 'contents/redux/store';
import { GlobalContext } from 'contents/MainFrame';
import React, { useContext, KeyboardEvent, useEffect, useState, useRef } from 'react';
import { ActionType } from 'contents/redux/reducer';


const ScoreContSourceTab = () => {
    const { state, dispatch, dispatcher } = useContext(GlobalContext);
    const ref = useRef({} as HTMLTextAreaElement);

    useEffect(() => {
        if (state.scoreTabIndex == 2 && state.noteData.dataList.length != 0) {
            ref.current.value = JSON.stringify(state.noteData);
        }
    }, [state.scoreTabIndex]);

    return (
        <div id="scorecont-sourcetab-root">
            <textarea ref={ref}></textarea>
            <button
                onClick={() => {
                    dispatcher.score.setNoteData(JSON.parse(ref.current.value));
                    dispatch(ActionType.SELECT_NOTE_ELEMENT, {index: 2});
                    dispatch(ActionType.SELECT_SCORETAB, { index: 1 });
                }}
            >Load score data...</button>
        </div>
    );
}

export default ScoreContSourceTab;
