import { API_DOMAIN } from 'contents/common/define';
import { sendQueryRequestToAPI } from 'contents/common/utils';
import { GlobalContext } from 'contents/MainFrame';
import { useContext, useEffect, useRef, useState } from 'react';
import './LoginFrame.css';

const LoginFrame = () => {

    const ref = useRef({} as HTMLDivElement);

    const [loginType, setLoginType] = useState<'guest' | 'login' | 'create'>('guest');
    const { state, dispatcher } = useContext(GlobalContext);
    const [userName, setUserName] = useState('');
    const [password, setPassword] = useState('');
    const [passConfirm, setPassConfirm] = useState('');
    const [message, setMessage] = useState(' ');
    const [isError, setError] = useState(false);

    const [isLoading, setLoading] = useState(false);

    const enterDisable = (): boolean => {
        switch (loginType) {
            case 'guest': return false;
            case 'login': {
                return userName == '' || userName.length < 5 || userName.length > 15 || !isHanEisu(userName) ||
                    password == '' || password.length < 5 || password.length > 15 || !isHanEisu(password);
            }
            case 'create': {
                return userName.length < 5 || userName.length > 15 || !isHanEisu(userName) ||
                    password.length < 5 || password.length > 15 || !isHanEisu(password) ||
                    passConfirm.length < 5 || passConfirm.length > 15 || !isHanEisu(passConfirm);
            }
        }
    }
    return (<>
        <div id="logo"><span>Virtual Harmonizer</span></div>
        <div id="login-frame" ref={ref}>
            <div className="guide">
                <span>Start to Virtual Harmonizer</span>
            </div>
            <div className="type">
                <LoginTypeSwitch
                    curLoginType="guest"
                    name="guest" loginType={loginType}
                    setLoginType={setLoginType}
                    resetError={() => setMessage('')}
                />
                <LoginTypeSwitch
                    curLoginType="login"
                    name="login" loginType={loginType}
                    setLoginType={setLoginType}
                    resetError={() => setMessage('')}
                />
                <LoginTypeSwitch
                    curLoginType="create"
                    name="create account" loginType={loginType}
                    setLoginType={setLoginType}
                    resetError={() => setMessage('')}
                />
            </div>
            <div className="body">
                <TextForm
                    label="user name"
                    stateValue={userName}
                    stateSetter={setUserName}
                    isMask={false}
                    isDisable={!['login', 'create'].includes(loginType)}
                />
                <TextForm
                    label="password"
                    stateValue={password}
                    stateSetter={setPassword}
                    isMask={true}
                    isDisable={!['login', 'create'].includes(loginType)}
                />
                <TextForm
                    label="password confirm"
                    stateValue={passConfirm}
                    stateSetter={setPassConfirm}
                    isMask={true}
                    isDisable={!['create'].includes(loginType)}
                />
            </div>
            <div className="message">
                <span className={isError ? 'error' : ''}>{message}</span>
            </div>
            <div
                className={['opr', (enterDisable() || isLoading) ? 'disable' : ''].join(' ')}
            >
                <button
                    onClick={() => {

                        const login = (id: number, name: string) => {
                            ref.current.style.opacity = '0';
                            setTimeout(() => {
                                dispatcher.setLoginParams({
                                    ...state.login,
                                    isLogin: true,
                                    id,
                                    name
                                });
                            }, 300);
                        }
                        setLoading(true);
                        setError(false);
                        setMessage('Connect to server...');
                        switch (loginType) {
                            case 'guest': {
                                login(-1, 'Guest');
                                setLoading(false);
                            }
                                break;
                            case 'login': {

                                sendQueryRequestToAPI('select', `SELECT * FROM user_tbl where name = '${userName}' and password = '${password}'`)
                                    .then(response => {
                                        return response.json();
                                    })
                                    .then(results => {
                                        console.log(results.length);
                                        const isExist = results.length > 0;
                                        if (isExist) {
                                            login(results[0].id, results[0].name);
                                        } else {
                                            setError(true);
                                            setMessage('The login attempt failed. Either the user ID or password is invalid.');
                                        }
                                        setLoading(false);
                                    })
                                    .catch(error => {
                                        console.log(error + "");
                                        setLoading(false);
                                    });
                            }
                                break;
                            case 'create': {

                                if (password != passConfirm) {
                                    setError(true);
                                    setMessage(`Confirmation passwords don't match.`);
                                    setLoading(false);
                                    break;
                                }

                                (async () => {
                                    let response = await sendQueryRequestToAPI('select', `SELECT count(*) as cnt FROM user_tbl where name = '${userName}'`);
                                    let results = await response.json();
                                    console.log(JSON.stringify(results[0].cnt));
                                    const isExist = results[0].cnt > 0;
                                    if (isExist) {
                                        setError(true);
                                        setMessage('That user id has already been recorded.');
                                    } else {
                                        response = await sendQueryRequestToAPI('update', `INSERT INTO user_tbl(name, password) VALUES('${userName}', '${password}')`);
                                        results = await response.json();
                                        setMessage('');
                                        alert('You have successfully created an account!');
                                        setLoginType('login');
                                    }
                                    setLoading(false);
                                })();
                            }
                                break;
                        }
                    }}
                >
                    enter
                </button>
            </div>
        </div>
    </>);
}

const LoginTypeSwitch = (
    props: {
        name: string,
        curLoginType: string,
        loginType: string,
        setLoginType: Function,
        resetError: Function
    }
) => {
    const isSelected = props.loginType == props.curLoginType;
    return (
        <button
            className={isSelected ? 'active' : ''}
            onClick={() => {
                if (!isSelected) {
                    props.setLoginType(props.curLoginType);
                    props.resetError();
                }
            }}
        >
            {props.name}
        </button>
    );
}


const TextForm = (
    props: {
        label: string,
        stateValue: string,
        stateSetter: Function,
        isMask: boolean,
        isDisable: boolean
    }
) => {
    useEffect(() => {
        if (props.isDisable) {
            props.stateSetter('');
        }
    }, [props.isDisable]);
    const strLen = props.stateValue.length;
    return (
        <div className={['form', props.isDisable ? 'disable' : ''].join(' ')}>
            <span>{props.label}:</span>
            <input
                className={[
                    !props.isDisable && strLen == 0 ? 'required' : '',
                    !props.isDisable && (strLen < 5 || strLen > 15) ? 'range' : '',
                    !props.isDisable && !isHanEisu(props.stateValue) ? 'ban' : ''
                ].join(' ')}
                type={props.isMask ? 'password' : 'text'}
                value={props.stateValue}
                onChange={(e) => props.stateSetter(e.target.value)}
            />
        </div>
    );
}

const isHanEisu = (str: string) => {
    str = (str == null) ? "" : str;
    if (str.match(/^[A-Za-z0-9_!@]*$/)) {
        return true;
    } else {
        return false;
    }
}

export default LoginFrame;