import { SymbolParams } from "contents/common/define";
import { _Text } from "contents/common/Styles";
import { BackingEditorState } from "contents/redux/store";

export const StructMatrix = (props: {
    paletteState: BackingEditorState;
    structs: string[];
    symbol: SymbolParams;
    degreeIndex: number;
    testPlaySound: Function;
    setUseChannels: Function
}) => {
    const useChannels = props.paletteState.useChannels;

    const structTable: JSX.Element[] = [];
    const ChordStructMax = 7;
    const OctaveMax = 8;
    
    // 
    useChannels.sort((a, b) => {
        return (a.octave * 12 + a.structIndex) - (b.octave * 12 + b.structIndex);
    });
    const overVoicingList = useChannels.slice(props.paletteState.channelRowSize);
    for (let i = 0; i < ChordStructMax; i++) {
        // for (let i = ChordStructMax - 1; i >= 0; i--) {
        const isUse = i < props.structs.length;
        const cols: JSX.Element[] = [];
        if (isUse) {
            const soundName = props.structs[i];
            const hasDom = props.structs.length - props.symbol.intervals.length == 1;
            const index = hasDom ? i - 1 : i;
            const interval = index == -1 ? -1 : props.symbol.intervals[index];
            const relation = index == -1 ? 'dom' : props.symbol.struct[index];
            // 
            cols.push(
                <div key={0} className="label">
                    <span>{relation}[{interval}]: <_Text color="#000">{soundName}</_Text></span>
                </div>
            );
            // 
            for (let j = 0; j < OctaveMax; j++) {
                // for (let j = OctaveMax - 1; j >= 0; j--) {
                const soundIndex = props.degreeIndex + interval + j * 12;
                const octave = Math.floor(soundIndex / 12);
                const soundFullName = soundName + octave;
                const isNone = soundIndex < 9 || soundIndex >= 97;
                let isSelected = false;
                useChannels.forEach((channel) => {
                    if (channel.octave == j && channel.structIndex == i) {
                        isSelected = true;
                    }
                });
                // 
                // const isLimit = props.paletteState.useChannels.length >= props.paletteState.channelRowSize && !isSelected;
                const isLimit = useChannels.length >= 12 && !isSelected;

                // if(isSelected) {
                //     useChannels.forEach((channel, k) =>{
                //         if (channel.octave == j && channel.structIndex == i) {

                //         }
                //     });
                // }
                let isOverVoicing = false;
                overVoicingList.forEach((channel) => {
                    if (channel.octave == j && channel.structIndex == i) {
                        isOverVoicing = true;
                    }
                });

                cols.push(
                    <div
                        key={j + 1}
                        className={[
                            'col',
                            (isNone || isLimit ? 'disable' : ''),
                            (isSelected ? 'active' : ''),
                            (isOverVoicing ? 'over' : '')
                        ].join(' ')}
                        onClick={() => {
                            const list = props.paletteState.useChannels;
                            if (!isSelected) {
                                list.push({ octave: j, structIndex: i });
                            } else {
                                let pos = -1;
                                list.forEach((channel, index) => {
                                    if (channel.octave == j && channel.structIndex == i) {
                                        pos = index;
                                    }
                                });
                                list.splice(pos, 1);
                            }
                            list.sort((a, b) => {
                                const aVal = a.octave * 8 + a.structIndex;
                                const bVal = b.octave * 8 + b.structIndex;
                                return aVal - bVal;;
                            });
                            props.paletteState.useChannels = list;
                            props.setUseChannels(props.paletteState.useChannels);
                        }}
                        onContextMenu={() => {
                            props.testPlaySound(soundFullName);
                        }}
                    >
                        <span>{!isNone ? soundFullName : '-'}</span>
                    </div>
                );
            }
        } else {
            // 
            cols.push(
                <div key={0} className="label"></div>
            );
            for (let j = 0; j < OctaveMax; j++) {
                cols.push(
                    <div key={j + 1} className="col"></div>
                );
            }
        }
        structTable.push(
            <div
                key={i}
                className={
                    'record' +
                    (!isUse ? ' disable' : '')
                }
            >
                {cols}
            </div>
        );
    }

    return (
        <div className="struct-table">
            {structTable}
        </div>
    );
}

export default StructMatrix;