import { useState, useEffect } from 'react';

import './TabbedPane.css';

type TabbedPaneProps = {
    groupName: string;
    activeNo: number;
    tabElements: TabElements[];
    selectTabIndex?: Function;
}

type TabElements = {
    name: string;
    disable: boolean;
    cont: JSX.Element;
}

const TabbedPane = (props: TabbedPaneProps) => {
    const [activeNo, setActiveNo] = useState(props.activeNo);

    // const [tabList, setTabList] = useState<JSX.Element[]>([]);
    // const [contList, setContList] = useState<JSX.Element[]>([]);

    // 
    const selectTabIndex = (index: number) => {
        if (props.selectTabIndex !== undefined) {
            props.selectTabIndex(index);
        } else {
            setActiveNo(index);
        }
    };

    // useEffect(() => {
    //     const tabList: JSX.Element[] = [];
    //     const contList: JSX.Element[] = [];
    //     props.tabElements.forEach((tab, index) => {
    //         tabList.push(
    //             <TabLabel
    //                 key={index} name={tab.name} isActive={index == activeNo} isDisable={tab.disable}
    //                 selectTab={() => selectTabIndex(index)}
    //             />)
    //             ;
    //         contList.push(
    //             <TabContents key={index} cont={tab.cont} isActive={index == activeNo} />
    //         );
    //     });
    //     setTabList(tabList);
    //     setContList(contList);
    //     console.log('create TabbedPane');
    // }, [activeNo]);

    useEffect(() => {
        setActiveNo(props.activeNo);
    }, [props.activeNo]);

    return (
        <div id={props.groupName}>
            <div className="tab-list">
                {/* {tabList} */}
                {
                    props.tabElements.map((tab, index) => {
                        return <TabLabel
                            key={index} name={tab.name} isActive={index == activeNo} isDisable={tab.disable}
                            selectTab={() => selectTabIndex(index)}
                        />
                    })
                }
            </div>
            <div className="cont-list">
                {/* {contList} */}
                {
                    props.tabElements.map((tab, index) => {
                        return <TabContents key={index} cont={tab.cont} isActive={index == activeNo} />
                    })
                }
            </div>
        </div>
    );
}


const TabLabel = (props: {
    name: string;
    isActive: boolean;
    isDisable: boolean;
    selectTab: Function;
}) => {
    const classValues = [];
    classValues.push('tab-menu');
    classValues.push(props.isActive ? 'active' : '');
    classValues.push(props.isDisable ? 'tab-disable' : '');
    return (
        <div className={classValues.join(' ')} onClick={() => props.selectTab()}>
            <span>{props.name}</span>
        </div>
    );
}

const TabContents = (props: {
    isActive: boolean;
    cont: JSX.Element;
}) => {
    return (
        <div className={[
            'tab-cont',
            props.isActive ? '' : 'hidden'].join(' ')}
        >
            {props.cont}
        </div>
    );
}

export default TabbedPane;