
import { RYTHM_LIST, TimeSignatures } from 'contents/common/define';
import { GlobalContext } from 'contents/MainFrame';
import { ActionType } from 'contents/redux/reducer';
import { ChangeType, FloatWndState, ModulateRelation, TempoRelation } from 'contents/redux/store';
import { useContext, useEffect, useRef, useState } from 'react';
import './ChangeEditor.css';

const ChangeEditor = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);


    const ref = useRef({} as HTMLDivElement);
    useEffect(() => {
        // dispatch(ActionType.GET_REFFER, { reffers: {...state.reffers, chordPalette: ref.current }});
        // dispatch(ActionType.GET_REFFER_SECTION_PALETTE, { reffer: ref.current });
    }, []);

    const [changeType, setChangeType] = useState<string>('');
    const [modulateRelation, setModulateRelation] = useState<string>('');
    const [domminantValue, setDomminantValue] = useState<'' | number>('');
    const [keyAdjust, setKeyAdjust] = useState<'' | number>('');
    const [tempoRelation, setTempoRelation] = useState<string>('');
    const [tempoRate, setTempoRate] = useState<'' | number>('');
    const [tempoDiff, setTempoDiff] = useState<'' | number>('');
    const [tempoAbsolute, setTempoAbsolute] = useState<'' | number>('');
    const [beat, setBeat] = useState<string>('');

    const [isInputOK, setInputOK] = useState<boolean>(false);

    const checkInput = () => {
        setInputOK(false);
        switch (changeType) {
            case ChangeType.MODULATE: {
                switch (modulateRelation) {
                    case ModulateRelation.DOMINANT: {
                        if (domminantValue !== '') setInputOK(true);
                    }
                        break;
                    case ModulateRelation.KEY: {
                        if (keyAdjust !== '') setInputOK(true);
                    }
                        break;
                    case ModulateRelation.RELATIVE: {
                        setInputOK(true);
                    }
                        break;
                    case ModulateRelation.PARALLEL: {
                        setInputOK(true);
                    }
                        break;
                }
            }
                break;
            case ChangeType.TEMPO: {
                switch (tempoRelation) {
                    case TempoRelation.DIFF: {
                        if (tempoDiff !== '') setInputOK(true);
                    }
                        break;
                    case TempoRelation.RATE: {
                        if (tempoRate !== '') setInputOK(true);
                    }
                        break;
                    case TempoRelation.ABSOLUTE: {
                        if (tempoAbsolute !== '') setInputOK(true);
                    }
                        break;
                }
            }
                break;
            case ChangeType.BEAT: {
                if (beat !== '') setInputOK(true);
            }
                break;
        }
    }

    useEffect(() => {
        checkInput();
    }, [changeType, modulateRelation, domminantValue, keyAdjust,
        tempoRelation, tempoDiff, tempoRate, tempoAbsolute, beat
    ]);

    // const KeyFilter = (value: string) => isNaN(Number(value));
    let itemsJsxList: JSX.Element[] = [];
    itemsJsxList.push(
        getItemSelectorJsx(itemsJsxList.length, 'Change Type', changeType,
            (value: string) => {
                setChangeType(value);
                if (value !== ChangeType.MODULATE) {
                    setModulateRelation('');
                    setDomminantValue('');
                    setKeyAdjust('');
                }
                if (value !== ChangeType.TEMPO) {
                    setTempoRelation('');
                    setTempoRate('');
                    setTempoDiff('');
                    setTempoAbsolute('');
                }
                if (value !== ChangeType.BEAT) {
                    setBeat('');
                }
            },
            // 
            [{ value: '', display: '' }].concat(
                Object.values(ChangeType).map((item) => {
                    return { value: item.toString(), display: item.toString() };
                })
            ),
            false
        )
    );
    itemsJsxList.push(
        getItemSelectorJsx(itemsJsxList.length, 'Modulate Relation', modulateRelation,
            (value: string) => {
                setModulateRelation(value);

                if (value !== ModulateRelation.KEY) {
                    setKeyAdjust('');
                }
                if (value !== ModulateRelation.DOMINANT) {
                    setDomminantValue('');
                }
            },
            // 
            [{ value: '', display: '' }].concat(
                Object.values(ModulateRelation).map((item) => {
                    const str = item.toString();
                    return { value: str, display: str };
                })
            ),
            changeType !== ChangeType.MODULATE.toString()
        )
    );
    itemsJsxList.push(
        getItemNumberRangeJsx(itemsJsxList.length, 'Domminant Value', domminantValue, -3, 3,
            (value: string) => {
                setDomminantValue(value as '' | number);
            },
            modulateRelation !== ModulateRelation.DOMINANT.toString()
        )
    );
    itemsJsxList.push(
        getItemNumberRangeJsx(itemsJsxList.length, 'Key Adjust', keyAdjust, -3, 3,
            (value: string) => {
                setKeyAdjust(value as '' | number);
            },
            modulateRelation !== ModulateRelation.KEY.toString()
        )
    );
    itemsJsxList.push(
        getItemSelectorJsx(itemsJsxList.length, 'Tempo Relation', tempoRelation,
            (value: string) => {
                setTempoRelation(value);

                if (value !== TempoRelation.RATE) {
                    setTempoRate('');
                }
                if (value !== TempoRelation.DIFF) {
                    setTempoDiff('');
                }
                if (value !== TempoRelation.ABSOLUTE) {
                    setTempoAbsolute('');
                }
            },
            // 
            [{ value: '', display: '' }].concat(
                Object.values(TempoRelation).map((item) => {
                    const str = item.toString();
                    return { value: str, display: str };
                })
            ),
            changeType !== ChangeType.TEMPO.toString()
        )
    );
    itemsJsxList.push(
        getItemNumberRangeJsx(itemsJsxList.length, 'Tempo Rate', tempoRate, 20, 200,
            (value: string) => {
                setTempoRate(value as '' | number);
            },
            tempoRelation !== TempoRelation.RATE.toString()
        )
    );
    itemsJsxList.push(
        getItemNumberRangeJsx(itemsJsxList.length, 'Tempo Diff', tempoDiff, -150, 150,
            (value: string) => {
                setTempoDiff(value as '' | number);
            },
            tempoRelation !== TempoRelation.DIFF.toString()
        )
    );
    itemsJsxList.push(
        getItemNumberRangeJsx(itemsJsxList.length, 'Tempo Absolute', tempoAbsolute, 30, 180,
            (value: string) => {
                setTempoAbsolute(value as '' | number);
            },
            tempoRelation !== TempoRelation.ABSOLUTE.toString()
        )
    );
    itemsJsxList.push(
        getItemSelectorJsx(itemsJsxList.length, 'Beat', beat,
            (value: string) => {
                setBeat(value);
            },
            // 
            [{ value: '', display: '' }].concat(
                Object.values(TimeSignatures).map((item) => {
                    const key = item.toString();
                    return { value: key, display: key };
                })
            ),
            changeType !== ChangeType.BEAT.toString()
        )
    );

    let operation: JSX.Element = <></>;
    operation = (<>
        <div
            onClick={() => {
                dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            }}>
            <span>Cancel</span>
        </div>
        <div
            className={[
                isInputOK ? '' : 'disable'
            ].join('')}
            onClick={() => {
                dispatcher.change.applyChangeParams(
                    changeType as ChangeType,
                    modulateRelation === '' ? null : modulateRelation as ModulateRelation,
                    domminantValue === '' ? 0 : domminantValue as number,
                    keyAdjust === '' ? 0 : keyAdjust as number,
                    tempoRelation === '' ? null : tempoRelation as TempoRelation,
                    tempoDiff === '' ? 0 : tempoDiff as number,
                    tempoRate === '' ? 0 : tempoRate as number,
                    tempoAbsolute === '' ? 0 : tempoAbsolute as number,
                    beat === '' ? null : beat as TimeSignatures
                );
            }}>
            <span>Enter</span>
        </div>
    </>);


    return (

        <div
            id="change-editor"
            className={[
                'floatwnd',
                state.noteState.floatwndState == FloatWndState.CHANGE_PALETTE ? ' active' : ''
            ].join(' ')}
            onClick={() => {
            }}
        >
            <div className="items">
                {itemsJsxList}
            </div>
            <div className="operation">
                {operation}
            </div>
        </div>
    );
}

const getItemSelectorJsx = (key: number, label: string, value: string,
    callback: (value: string) => void,
    list: { value: string, display: string }[], isDisable: boolean
): JSX.Element => {
    const optionsJsx: JSX.Element[] = [];
    list.forEach((data) => {
        optionsJsx.push(
            <option key={optionsJsx.length} value={data.value}>{data.display}</option>
        );
    });
    return (
        <div
            key={key}
            className={[
                'record',
                isDisable ? 'disable' : ''
            ].join(' ')}
        >
            <div className="label">{label}</div>
            <div className="value">
                <select
                    className={
                        !isDisable && value === '' ? 'error' : ''
                    }
                    value={value}
                    onChange={(e) => {
                        callback(e.target.value);
                    }}
                >
                    {optionsJsx}
                </select>
            </div>
        </div>
    );
}

const getItemNumberRangeJsx = (key: number, label: string, value: '' | number,
    min: number, max: number, callback: (value: string) => void, isDisable: boolean
): JSX.Element => {
    return (
        <div
            key={key}
            className={[
                'record',
                isDisable ? 'disable' : ''
            ].join(' ')}
        >
            <div className="label">{label}</div>
            <div className="value">
                <input type="number"
                    className={
                        !isDisable && (value === '' || !(value >= min && value <= max)) ? 'error' : ''
                    }
                    value={value} min={min} max={max}
                    onChange={(e) => {
                        callback(e.target.value);
                    }}
                />
            </div>
        </div>
    );
}

export default ChangeEditor;