
import { getAdjustSusValue } from 'contents/common/utils';
import { GlobalContext } from 'contents/MainFrame';
import { DetailChord, TypeNoteElement } from 'contents/redux/store';
import { useContext, useEffect, useRef, useState, VFC } from 'react';
import './FooterTiming.css';
import { makeStyles } from "@material-ui/core/styles";
import styled from 'styled-components';

const MEM_WIDTH = 16;
const FIXED_BLANK = 100;

const StyleTest = styled.div`
    font-size: 0;
    display: inline-inline-block;
`;



const FooterTiming: VFC = () => {

    const { state, dispatcher } = useContext(GlobalContext);
    const dataList = state.noteData.dataList;
    const focusIndex = state.noteState.focusIndex;
    const dataCount = state.noteData.dataList.length;

    /** Ref */
    const scrollRef = useRef({} as HTMLDivElement);

    const [blockJsxList, setBlockJsxList] = useState([] as JSX.Element[]);
    const [measureJsxList, setMeasureJsxList] = useState([] as JSX.Element[]);
    const [total, setTotal] = useState(0);

    const chordDetail = dataList[focusIndex].type == TypeNoteElement.Chord ?
        (dataList[focusIndex].detail as DetailChord) : null;
    const sustain = chordDetail == null ? 0 : chordDetail.sustain;
    const headAdjust = chordDetail == null ? 0 : chordDetail.headAdjust;
    const tailAdjust = chordDetail == null ? 0 : chordDetail.tailAdjust;
    useEffect(() => {
        let total = 0;
        const blockJsxList: JSX.Element[] = [];
        const measureJsxList: JSX.Element[] = [];
        const dataList = state.noteData.dataList;

        dataList.forEach((data, i) => {

            if (data.type == TypeNoteElement.Init) {
                blockJsxList.push(
                    <div
                        key={blockJsxList.length}
                        className={[
                        ].join(' ')}
                        style={{ width: `${FIXED_BLANK}px` }}
                    />
                )
            }
            if (data.type == TypeNoteElement.Chord) {
                const detail = data.detail as DetailChord;
                const len = detail.sustain + detail.headAdjust + detail.tailAdjust;
                const width = len * MEM_WIDTH * 4;
                const isCur = focusIndex == i;
                const isNear = focusIndex == i - 1 || focusIndex == i + 1;
                blockJsxList.push(
                    <div
                        key={blockJsxList.length}
                        className={[
                            isCur ? 'active' : '',
                            isNear ? 'near' : '',
                        ].join(' ')}
                        style={{ width: `${width - 1}px` }}
                    >
                        <div className="sus">
                            <span>{detail.sustain}</span>
                        </div>
                        <div className="adjust">
                            <span className={getAdjustCls(detail.headAdjust)}>
                                {getAdjustSusValue(detail.headAdjust)}
                            </span>
                            <span className={getAdjustCls(detail.tailAdjust)}>
                                {getAdjustSusValue(detail.tailAdjust)}
                            </span>
                        </div>
                    </div >
                );
                if (isCur) {
                    scrollRef.current.scrollTo({ left: total * MEM_WIDTH - FIXED_BLANK, behavior: 'smooth' });
                }
                total += len * 4;
            }
        });

        // measureJsxList.push(
        //     <div key={measureJsxList.length} className="blank" style={{ left: `${0}px` }} />
        // )
        for (let i = 0; i < total; i++) {
            let name = '';
            const isBar = i % 16 == 0;
            /**  */
            const barCount = 1 + (i / 16);
            if (isBar) {
                name = 'mem-bar';
            } else {
                switch (i % 4) {
                    case 0: name = 'mem4'; break;
                    case 2: name = 'mem8'; break;
                    default: name = 'mem16'; break;
                }
            }
            measureJsxList.push(
                <div key={measureJsxList.length} className={name} style={{ left: `${FIXED_BLANK + i * MEM_WIDTH}px` }}>{
                    i % 16 == 0 ?
                        <div className="bar" style={{ left: `${-14}px`, top: '24px' }}>{barCount}</div>
                        :
                        <></>
                }</div>
            );
        }
        // alert(measureJsxList.length);
        setBlockJsxList(blockJsxList);
        setMeasureJsxList(measureJsxList);
        setTotal(total);
    }, [dataCount, focusIndex, sustain, headAdjust, tailAdjust]);

    if (chordDetail == null) {
        return (<></>);
    }

    const totalWidth = FIXED_BLANK * 2 + total * MEM_WIDTH;
    return (
        <div id="footer-timing" className="footer-child">
            <div className="scroll" ref={scrollRef}>
                <div className="blocks" style={{ width: `${totalWidth}px` }}>
                    {blockJsxList}
                </div>
                <div className="measure" style={{ width: `${totalWidth}px` }}>
                    {measureJsxList}
                </div>
            </div>
        </div>
    );
}

const getAdjustCls = (value: number): string => {
    let ret = '';
    if (value != 0) {
        if (value > 0) ret = 'aug';
        else ret = 'dim';
    }
    return ret;
}

export default FooterTiming;