import { DEGREE12_FLAT_LIST, DEGREE12_SHARP_LIST, KEY12_FLAT_LIST, KEY12_SHARP_LIST, SymbolKey, SymbolParams, SYMBOL_PARAMS_LIST } from "contents/common/define";
import TabbedPane from "contents/common/TabbedPane";
import { getSymbolParams } from "contents/common/utils";
import { GlobalContext } from "contents/MainFrame";
import { ActionType } from "contents/redux/reducer";
import { DetailChord, FloatWndState, ChordPaletteLine, TypeNoteElement, CHORD_MODE, ChordPaletteState } from "contents/redux/store";
import { useContext, useEffect, useRef, useState } from "react";
// import styled from 'styled-components';
// npm i --save-dev @types/styled-components
import './ChordSelector.css';

const ChordSelector = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    // const [degreeIndex, setDegreeIndex] = useState(0);
    // const [isFlat, setFlat] = useState(false);
    // const [symbolKey, setSymbolKey] = useState(SYMBOL_PARAMS_LIST[0].key);
    // const [sustain, setSustain] = useState(4);

    /**  */
    const ref = useRef<HTMLDivElement>(null);
    const canvasRef = useRef<HTMLCanvasElement>(null);

    const editorState = state.noteState.chordPaletteState;
    const focusIndex = state.noteState.focusIndex;
    const chordDetail = state.noteData.dataList[focusIndex].detail as DetailChord;
    const cache = state.noteState.cacheList[focusIndex];
    useEffect(() => {
        // dispatch(ActionType.GET_REFFER, { reffers: {...state.reffers, chordPalette: ref.current }});
        dispatch(ActionType.GET_REFFER_CHORD_PALETTE, { reffer: ref.current });
        const canvas: any = canvasRef.current;
        canvas.width = 590;
        canvas.height = 66;
    }, []);

    useEffect(() => {
        if (state.noteState.floatwndState == FloatWndState.CHORD_PALETTE) {
            updatePianoView(
                editorState.molecule.rootIndex,
                editorState.denominator == null ? -1 : editorState.denominator.rootIndex,
                editorState.symbolKey
            );
        }
    }, [editorState, state.noteState.floatwndState]);

    const updatePianoView = (molRootIndex: number, denRootIndex: number, symbolKey: SymbolKey) => {
        const ctx = (canvasRef.current as HTMLCanvasElement).getContext('2d') as CanvasRenderingContext2D;

        const keyIndex = (cache.init.keyIndex + molRootIndex) % 12;
        const intervalList = (getSymbolParams(symbolKey) as SymbolParams).intervals;
        const list = intervalList.map((value) => keyIndex + value);
        let denIndex = -1;
        if (denRootIndex != -1) {
            denIndex = cache.init.keyIndex + denRootIndex;
        }
        ctx.fillStyle = '#008';
        ctx.fillRect(0, 0, 594, 70);
        const WIDTH = 34;
        const HEIGHT = 60;
        const BLACK_RATE_W = 0.5;
        const BLACK_RATE_H = 0.6;
        const MARGIN_W = 1;
        const START_ADJUST = 1;
        let posX = 0;
        const keyboadList: {
            posX: number;
            fillStyle: string;
            width: number;
            height: number;
            isBlack: boolean;
        }[] = [];
        for (let i = 0; i < 27; i++) {
            let color = '#eee';
            let width = WIDTH;
            let height = HEIGHT;
            const isBlack = [1, 3, 6, 8, 10].includes(i % 12);
            if (isBlack) {
                color = '#111';
                width = WIDTH * BLACK_RATE_W;
                height = HEIGHT * BLACK_RATE_H;
            }
            if (list.includes(i)) {
                color = '#fa0';
            }
            if (denIndex == i) {
                color = '#ff0000';
            }
            keyboadList.push({ posX: 15 + posX, fillStyle: color, width, height, isBlack });

            switch (i % 12) {
                case 0: case 2: case 5: case 7: case 9:
                    //
                    posX += WIDTH - WIDTH * BLACK_RATE_W / 2;
                    break;
                case 1: case 3: case 6: case 8: case 10:
                    //
                    posX += WIDTH * BLACK_RATE_W / 2 + MARGIN_W;
                    break;
                default:
                    //
                    posX += WIDTH + MARGIN_W;
            }
        }
        keyboadList.forEach((keyboad) => {
            if (!keyboad.isBlack) {
                ctx.fillStyle = keyboad.fillStyle;
                ctx.fillRect(keyboad.posX, 3, keyboad.width, keyboad.height);
            }
        });
        keyboadList.forEach((keyboad) => {
            if (keyboad.isBlack) {
                ctx.fillStyle = keyboad.fillStyle;
                ctx.fillRect(keyboad.posX, 3, keyboad.width, keyboad.height);
                ctx.fillStyle = '#f00';
                ctx.strokeRect(keyboad.posX, 3, keyboad.width, keyboad.height);
            }
        });
        ctx.save();
    }

    const chordList: JSX.Element[] = [];
    const symbolList: JSX.Element[] = [];
    const beatList: JSX.Element[] = [];
    let operationJsx: JSX.Element = <></>;
    const isDirect = state.noteData.chordMode == CHORD_MODE.DIRECT;
    if (state.noteState.floatwndState == FloatWndState.CHORD_PALETTE) {

        DEGREE12_SHARP_LIST.forEach((degS, i) => {
            const baseKey = cache.init.keyIndex;
            const keyIndex = (baseKey + i) % 12;
            const degF = DEGREE12_FLAT_LIST[i];
            const chdS = KEY12_SHARP_LIST[keyIndex];
            const chdF = KEY12_FLAT_LIST[keyIndex];
            const isHalf = [1, 3, 6, 8, 10].includes(i);
            const isSelected = editorState.molecule.rootIndex == i;
            const isDenominator = (editorState.denominator == null ? -1 : editorState.denominator.rootIndex) == i;
            const buildLabelSrc = (isFlat: boolean): JSX.Element => {
                return (
                    state.noteData.chordMode == CHORD_MODE.SCALE ?
                        <>
                            <span className="degree">{isFlat ? degF : degS}</span>
                            <span>&nbsp;(</span>
                            <span className="chord">{isFlat ? chdF : chdS}</span>
                            <span>)</span>
                        </>
                        :
                        <>
                            <span className="degree">{isFlat ? chdF : chdS}</span>
                        </>
                );
            };
            chordList.push(
                <div key={i}>{
                    !isHalf ?
                        <div
                            className={[
                                isSelected ? 'active' : '',
                                isDenominator ? 'denominator' : ''
                            ].join(' ')}
                            onClick={() => {
                                // 
                                const denominator = !isDenominator ? editorState.denominator : null;
                                dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                    params: {
                                        ...editorState,
                                        selectedLine: ChordPaletteLine.Root, molecule: { rootIndex: i, isFlat: false }, denominator
                                    } as ChordPaletteState
                                });
                                // updatePianoView(
                                //     i,
                                //     editorState.denominator == null ? -1 : editorState.denominator.rootIndex,
                                //     editorState.symbolKey
                                // );
                            }}
                            onContextMenu={() => {
                                if (isSelected) return;
                                const denominator = (!isDenominator ? { rootIndex: i, isFlat: false } : null);
                                dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                    params: {
                                        ...editorState,
                                        selectedLine: ChordPaletteLine.Root, denominator
                                    } as ChordPaletteState
                                });
                                // updatePianoView(
                                //     editorState.molecule.rootIndex,
                                //     i,
                                //     editorState.symbolKey
                                // );
                            }}
                        >
                            {buildLabelSrc(false)}
                        </div>
                        :
                        <>
                            <div
                                className={[
                                    "half",
                                    (isSelected && !editorState.molecule.isFlat ? ' active' : ''),
                                    (isDenominator && editorState.denominator != null && !editorState.denominator.isFlat ? ' denominator' : '')
                                ].join(' ')}
                                onClick={() => {
                                    // 
                                    const denominator = !(isDenominator && editorState.denominator != null && !editorState.denominator.isFlat)
                                        ? editorState.denominator : null;
                                    dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                        params: {
                                            ...editorState,
                                            selectedLine: ChordPaletteLine.Root, molecule: { rootIndex: i, isFlat: false }, denominator
                                        } as ChordPaletteState
                                    });
                                    // updatePianoView(
                                    //     i,
                                    //     editorState.denominator == null ? -1 : editorState.denominator.rootIndex,
                                    //     editorState.symbolKey
                                    // );
                                }}
                                onContextMenu={() => {
                                    if (isSelected && !editorState.molecule.isFlat) return;
                                    const denominator = (
                                        !(isDenominator && editorState.denominator != null && !editorState.denominator.isFlat)
                                            ? { rootIndex: i, isFlat: false } : null);
                                    dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                        params: {
                                            ...editorState,
                                            selectedLine: ChordPaletteLine.Root, denominator
                                        } as ChordPaletteState
                                    });
                                    // updatePianoView(
                                    //     editorState.molecule.rootIndex,
                                    //     i,
                                    //     editorState.symbolKey
                                    // );
                                }}
                            >
                                {buildLabelSrc(false)}
                            </div>
                            <div
                                className={[
                                    "half",
                                    (isSelected && editorState.molecule.isFlat ? ' active' : ''),
                                    (isDenominator && editorState.denominator != null && editorState.denominator.isFlat ? ' denominator' : '')
                                ].join(' ')}
                                onClick={() => {
                                    // 
                                    const denominator = !(isDenominator && editorState.denominator != null && editorState.denominator.isFlat)
                                        ? editorState.denominator : null;
                                    dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                        params: {
                                            ...editorState,
                                            selectedLine: ChordPaletteLine.Root, molecule: { rootIndex: i, isFlat: true }, denominator
                                        } as ChordPaletteState
                                    });
                                    // updatePianoView(
                                    //     i,
                                    //     editorState.denominator == null ? -1 : editorState.denominator.rootIndex,
                                    //     editorState.symbolKey
                                    // );
                                }}
                                onContextMenu={() => {
                                    if (isSelected && editorState.molecule.isFlat) return;
                                    const denominator = (
                                        !(isDenominator && editorState.denominator != null && editorState.denominator.isFlat)
                                            ? { rootIndex: i, isFlat: true } : null);
                                    dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                        params: {
                                            ...editorState,
                                            selectedLine: ChordPaletteLine.Root, denominator
                                        } as ChordPaletteState
                                    });
                                    // updatePianoView(
                                    //     editorState.molecule.rootIndex,
                                    //     i,
                                    //     editorState.symbolKey
                                    // );
                                }}
                            >
                                {buildLabelSrc(true)}
                            </div>
                        </>
                }</div>
            );
        });

        SYMBOL_PARAMS_LIST.forEach((symbol, i) => {
            // const isSelected = chordDetail.degreeIndex == i;
            symbolList.push(
                <div
                    key={i}
                    className={symbol.key == editorState.symbolKey ? 'active' : ''}
                    onClick={() => {
                        dispatch(ActionType.CHORD_PALETTE_SETTER, {
                            params: {
                                ...editorState,
                                selectedLine: ChordPaletteLine.Symbol, symbolKey: symbol.key
                            }
                        });
                        // updatePianoView(
                        //     editorState.molecule.rootIndex,
                        //     editorState.denominator == null ? -1 : editorState.denominator.rootIndex,
                        //     symbol.key
                        // );
                    }}
                >
                    <span>[{symbol.name}]</span>
                </div>

            );
        });

        for (let i = 0; i < 8; i++) {
            beatList.push(
                <div
                    key={i}
                    className={editorState.sustain >= i + 1 ? 'active' : ''}
                    onClick={() => {
                        dispatch(ActionType.CHORD_PALETTE_SETTER, {
                            params: {
                                ...editorState, selectedLine: ChordPaletteLine.Sustain, sustain: i + 1
                            }
                        });
                    }}
                />
            );
        }

        operationJsx = (<>
            <div
                onClick={() => {
                    dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                }}>
                <span>Cancel</span>
            </div>
            <div
                onClick={() => {
                    dispatcher.chord.setCurChordParam();
                }}>
                <span>Enter</span>
            </div>
        </>);
    }
    const getAdjustOprJsx = (type: 'h' | 't', offset: number): JSX.Element => {
        let [label, cls, isActive, callback] = type == 'h' ?
            ['h', 'head', editorState.headAdjust == offset,
                () => dispatch(ActionType.CHORD_PALETTE_SETTER, {
                    params: {
                        ...editorState, headAdjust: offset
                    }
                })] :
            ['t', 'tail', editorState.tailAdjust == offset,
                () => dispatch(ActionType.CHORD_PALETTE_SETTER, {
                    params: {
                        ...editorState, tailAdjust: offset
                    }
                })];
        if (offset >= 0) {
            label += (offset > 0 ? '+' : '');
        }
        return (
            <div
                className={[
                    cls,
                    isActive ? 'active' : ''
                ].join(' ')}
                onClick={callback}
            >
                <span>{label + offset}</span>
            </div>
        );
    }
    return (
        <div
            ref={ref}
            id="total-palette"
            className={'floatwnd' + (state.noteState.floatwndState == FloatWndState.CHORD_PALETTE ? ' active' : '')}
            onClick={() => {
            }}
        >
            <TabbedPane groupName="selector-tabs"
                tabElements={[
                    {
                        name: 'Scale', disable: isDirect, cont: (
                            <>
                            </>
                        )
                    },
                    {
                        name: 'Direct', disable: false, cont: (
                            <>
                                <div className={'chord-selector' + (editorState.selectedLine == ChordPaletteLine.Root ? ' active' : '')}>
                                    {chordList}
                                </div>
                                <div className={'symbol-selector' + (editorState.selectedLine == ChordPaletteLine.Symbol ? ' active' : '')}>
                                    {symbolList}
                                </div>
                            </>
                        )
                    }
                ]}
                activeNo={1}
            />
            <div className={'piano-view'}>
                <canvas ref={canvasRef} />
            </div>
            <div className={'beat-selector' + (editorState.selectedLine == ChordPaletteLine.Sustain ? ' active' : '')}>
                {beatList}
            </div>
            <div className={'adjuster' + (editorState.selectedLine == ChordPaletteLine.Adjuster ? ' active' : '')}>
                {getAdjustOprJsx('h', -0.5)}
                {getAdjustOprJsx('h', -0.25)}
                {getAdjustOprJsx('h', 0)}
                {getAdjustOprJsx('h', 0.25)}
                {getAdjustOprJsx('h', 0.5)}
                {getAdjustOprJsx('t', -0.5)}
                {getAdjustOprJsx('t', -0.25)}
                {getAdjustOprJsx('t', 0)}
                {getAdjustOprJsx('t', 0.25)}
                {getAdjustOprJsx('t', 0.5)}
            </div>
            <div className="operation">
                {operationJsx}
            </div>
        </div>
    );
}

export default ChordSelector;