import './ScoreTab.css';
import React, { useState, useContext, useRef, useEffect } from 'react';
import TabbedPane from 'contents/common/TabbedPane';

import { GlobalContext } from 'contents/MainFrame';
import ScoreContNoteTab from './contTabs/note/ScoreContNoteTab';
import { ActionType } from 'contents/redux/reducer';
import ScoreContLiblaryTab from './contTabs/library/ScoreContLibraryTab';
import ScoreContBackingTab from './contTabs/backing/ScoreContBackingTab';
import ScoreContSourceTab from './contTabs/source/ScoreContSourceTab';

const ScoreTab = () => {

    const { state, dispatch } = useContext(GlobalContext);

    return (
        <div id="scoretab-root">
            <TabbedPane groupName="score-tabs"
                tabElements={[
                    { name: 'Library', disable: true, cont: <ScoreContLiblaryTab /> },
                    {
                        name: 'Note', disable: state.noteData.dataList.length === 0,
                        cont: state.scoreTabIndex != 1 ? <></> : < ScoreContNoteTab />
                    },
                    { name: 'Source', disable: false, cont: <ScoreContSourceTab /> },
                    { name: 'Thema', disable: false, cont: <ScoreContBackingTab /> },
                    { name: 'Sheet Music', disable: true, cont: <div></div> },
                    { name: 'Preview', disable: true, cont: <div></div> }
                ]}
                activeNo={state.scoreTabIndex}
                selectTabIndex={(index: number) => dispatch(ActionType.SELECT_SCORETAB, { index: index })}
            />
        </div>
    );
}

export default ScoreTab;