
import TabbedPane from 'contents/common/TabbedPane';
import FooterTiming from './timing/FooterTiming';
import './NoteFooter.css';
import FooterEnsemble from './ensemble/FooterEnsemble';

const NoteFooter = () => {

    return (
        <div id="score-footer">
            <TabbedPane groupName="score-tabs"
                tabElements={[
                    { name: 'Progress', disable: false, cont: <FooterTiming /> },
                    { name: 'Target', disable: true, cont: <div></div> },
                    { name: 'Ensemble', disable: false, cont: <FooterEnsemble /> }
                ]}
                activeNo={0}
            // selectTabIndex={(index: number) => dispatch(ActionType.SELECT_SCORETAB, { index: index })}
            />
        </div>
    );
}

export default NoteFooter;