
import { Store, Action, TypeNoteElement, DetailChord, DetailSection, BackingLayer, BACKING_SIDE, ChannelParam, BACKING_LEN_TYPE, ScoreCache, BackingEditorState } from './store'

/**
 * 
 */
export enum ActionType {
    SET_LOGIN,
    SELECT_SCORETAB,
    ADD_SECTION,
    SET_DEGREE_INDEX,
    SET_SYMBOL_KEY,
    DETAIL_PARAMS_SETTER,
    GET_ELEMENT_REFFER,
    INIT_DEFAULT_ELEMENTS,
    SELECT_NOTE_ELEMENT,
    GET_REFFER,
    GET_REFFER_CONTEXT,
    GET_REFFER_CHORD_PALETTE,
    GET_REFFER_SECTION_PALETTE,
    GET_REFFER_BACKING_PALETTE,
    FLOATWND_STATE,
    CHORD_PALETTE_SETTER,
    SET_NOTE_DATA,
    SET_DATA_LIST,
    SET_NOTE_STATE,
    INIT_BACKING_PALETTE,
    SET_USE_CHANNELS,
    BACKING_TOGGLE_MATRIX
}

export const reducer = (state: Store, action: Action): Store => {
    const type: ActionType = action.type;
    const payload: any = action.payload;
    switch (type) {
        case ActionType.SET_LOGIN:
            return { ...state, login: payload.login };
        case ActionType.SELECT_SCORETAB:
            return { ...state, scoreTabIndex: payload.index };
        case ActionType.ADD_SECTION: {
            const dataList = state.noteData.dataList.slice();
            const detail = {} as DetailSection;
            detail.labelName = 'Section';
            dataList.splice(payload.index + 1, 0, {
                type: TypeNoteElement.Section,
                detail: detail
            });
            return {
                ...state, noteData: {
                    ...state.noteData, dataList: dataList
                }
            };
        }
        case ActionType.SET_DEGREE_INDEX: {
            const detail = state.noteData.dataList[payload.pos].detail as DetailChord;
            if(detail.molecule == null) {
                detail.molecule = {rootIndex: payload.degreeIndex, isFlat: false};
            } else {
                detail.molecule.rootIndex = payload.degreeIndex;
            }
            return {
                ...state, noteData: {
                    ...state.noteData, dataList: state.noteData.dataList
                }
            };
        }
        case ActionType.SET_SYMBOL_KEY: {
            const detail = state.noteData.dataList[payload.pos].detail as DetailChord;
            detail.symbolKey = payload.symbolKey;
            return {
                ...state, noteData: {
                    ...state.noteData, dataList: state.noteData.dataList
                }
            };
        }
        case ActionType.INIT_DEFAULT_ELEMENTS: {
            // const dataList = state.noteData.dataList;
            return {
                ...state, noteData: {
                    ...state.noteData, dataList: [
                        {
                            type: TypeNoteElement.Init,
                            detail: {
                                tempo: 80,
                                rythm: '4/4',
                                keyIndex: 0,
                                scale: 'major'
                            }
                        },
                        {
                            type: TypeNoteElement.Fixed,
                            detail: {
                                label: 'START'
                            }
                        },
                        {
                            type: TypeNoteElement.Section,
                            detail: {
                                labelName: 'Section'
                            }
                        },
                        {
                            type: TypeNoteElement.Fixed,
                            detail: {
                                label: 'END'
                            }
                        },
                    ]
                },
                noteState: {
                    ...state.noteState, cacheList: [{} as ScoreCache, {} as ScoreCache, {} as ScoreCache, {} as ScoreCache]
                }
            };
        }
        case ActionType.SELECT_NOTE_ELEMENT: {
            return {
                ...state, noteState: {
                    ...state.noteState, focusIndex: payload.index
                }
            };
        }
        case ActionType.GET_REFFER_CONTEXT: {
            return {
                ...state, reffers: { ...state.reffers, contextMneu: payload.reffer }
            };
        }
        case ActionType.GET_REFFER_CHORD_PALETTE: {
            return {
                ...state, reffers: { ...state.reffers, chordPalette: payload.reffer }
            };
        }
        case ActionType.GET_REFFER_SECTION_PALETTE: {
            return {
                ...state, reffers: { ...state.reffers, sectionPalette: payload.reffer }
            };
        }
        case ActionType.GET_REFFER_BACKING_PALETTE: {
            return {
                ...state, reffers: { ...state.reffers, backingPalette: payload.reffer }
            };
        }
        case ActionType.FLOATWND_STATE: {
            return {
                ...state, noteState: {
                    ...state.noteState, floatwndState: payload.state
                }
            };
        }
        case ActionType.CHORD_PALETTE_SETTER: {
            return {
                ...state, noteState: {
                    ...state.noteState, chordPaletteState: payload.params
                }
            };
        }
        case ActionType.DETAIL_PARAMS_SETTER: {
            const dataList = state.noteData.dataList.slice();
            const data = dataList[payload.pos];
            data.detail = payload.params;
            return {
                ...state, noteData: {
                    ...state.noteData, dataList: dataList
                }
            };
        }
        case ActionType.GET_REFFER: {
            return {
                ...state, reffers: payload.reffers
            };
        }
        case ActionType.SET_NOTE_DATA: {
            return {
                ...state, noteData: payload.data
            };
        }
        case ActionType.SET_DATA_LIST: {
            return {
                ...state, noteData: { ...state.noteData, dataList: payload.dataList }
            };
        }
        case ActionType.SET_NOTE_STATE: {
            return {
                ...state, noteState: payload.noteState
            };
        }
        case ActionType.GET_ELEMENT_REFFER: {
            const cache = state.noteState.cacheList[payload.pos];
            cache.reffer = payload.reffer;
            return {
                ...state, noteState: {
                    ...state.noteState, cacheList: state.noteState.cacheList
                }
            };
        }
        case ActionType.INIT_BACKING_PALETTE: {
            return {
                ...state, noteState: {
                    ...state.noteState, backingPaletteState: payload.backingPaletteState
                }
            };
        }
        case ActionType.SET_USE_CHANNELS: {
            const editorState = state.noteState.backingPaletteState as BackingEditorState;
            // const baseScript = backingPaletteState.layers;
            const useChannels = payload.useChannels as ChannelParam[];
            // const script: BackingLayer[] = new Array(2);
            // for (let i = 0; i < 2; i++) {
            //     const channelCount = backingPaletteState.useChannels.length;
            //     const lensSplitCount = baseScript[i].lens.length;
            //     const baseTable = baseScript[i].table;
            //     const table: boolean[][][] = new Array(channelCount);

            //     for (let y = 0; y < channelCount; y++) {
            //         if (y < baseTable.length) {
            //             table[y] = baseTable[y];
            //         } else {
            //             table[y] = new Array(lensSplitCount);
            //             for (let x = 0; x < lensSplitCount; x++) {
            //                 table[y][x] = new Array(1);
            //                 table[y][x][0] = false;
            //             }
            //         }
            //     }
            //     script[i] = {
            //         ...baseScript[i],
            //         table,
            //     };
            // }
            return {
                ...state, noteState: {
                    ...state.noteState, backingPaletteState: {
                        ...editorState,
                        useChannels,
                        // layers: script
                    }
                }
            };
        }
        case ActionType.BACKING_TOGGLE_MATRIX: {
            const editorState = state.noteState.backingPaletteState as BackingEditorState;
            const sideIndex: number = payload.sideIndex;
            const lenIndex: number = payload.lenIndex;
            const channelIndex: number = payload.channelIndex;
            const divIndex: number = payload.divIndex;
            const script = editorState.layers;
            script[sideIndex].table[channelIndex][lenIndex][divIndex] = payload.isUse;
            // script[sideIndex].table[channelIndex] = line;
            return {
                ...state, noteState: {
                    ...state.noteState, backingPaletteState: {
                        ...editorState, layers: script
                    }
                }
            };
        }
    }
}
