import { Dispatcher } from "contents/redux/dispatcher";
import { BackingEditorState, BACKING_LEN_TYPE, ChannelParam, DetailChord } from "contents/redux/store";
import { createContext } from "react";
import BackingOperation from "./BackingOperation";
import NotesMatrix from "./NotesMatrix";
import SplitMeasure from "./SplitMeasure";


export type ChannelBean = {
    soundIndex: number;
    soundFullName: string;
}

type BackingTableProps = {
    paletteState: BackingEditorState;
    dispatcher: Dispatcher;
    channelList: ChannelBean[];
    testPlaySound: Function;
}

const BackingTable = (props: BackingTableProps) => {

    const paletteState = props.paletteState;
    const sideIndex = paletteState.sideIndex;
    const curLayer = paletteState.layers[sideIndex];
    const dispatcher = props.dispatcher;
    const channelList = props.channelList;
    const testPlaySound = props.testPlaySound;


    const channelJsxList: JSX.Element[] = [];
    // 
    const blankAdjust = paletteState.channelRowSize - channelList.length;
    for (let i = 0; i < paletteState.channelRowSize; i++) {
        // 
        const curIndex = i - blankAdjust;
        // 
        const isExist = curIndex >= 0;
        let soundFullName = '-';
        if (isExist) {
            soundFullName = channelList[curIndex].soundFullName;
        }
        channelJsxList.push(
            <div key={i}>
                <div
                    className={[
                        'channel',
                        !isExist ? 'blank' : ''
                    ].join(' ')}
                    onClick={() => {
                        if (isExist) {
                            testPlaySound(soundFullName)
                        }
                    }}
                >
                    <span>{soundFullName}</span>
                </div>
                <div
                    className={[
                        'opr',
                        props.paletteState.channelRowSize == 1 ? 'disable' : ''
                    ].join(' ')}
                    onClick={() => {
                        const backing = props.paletteState;
                        const layers = backing.layers;
                        const index = backing.channelRowSize - (i + 1);
                        layers.forEach((layer) => {
                            layer.table.splice(index, 1);
                        });
                        backing.channelRowSize = backing.channelRowSize - 1;
                        dispatcher.backing.setPaletteState(props.paletteState);
                    }}
                >
                    <span>-</span>
                </div>
                <div
                    className={[
                        'opr',
                        props.paletteState.channelRowSize == 12 ? 'disable' : ''
                    ].join(' ')}
                    onClick={() => {
                        const backing = props.paletteState;
                        const layers = backing.layers;
                        const index = backing.channelRowSize - (i + 1);
                        layers.forEach((layer) => {
                            const record: boolean[][] = [];
                            for (let j = 0; j < layer.lens.length; j++) {
                                record.push([false]);
                            }
                            layer.table.splice(index, 0, record);
                        });
                        backing.channelRowSize = backing.channelRowSize + 1;
                        dispatcher.backing.setPaletteState(props.paletteState);
                    }}
                >
                    <span>+</span>
                </div>
            </div>
        );
    }

    // 
    const lens: JSX.Element[] = [];
    const volumeList: JSX.Element[] = [];
    curLayer.lens.forEach((lenBean, i) => {
        const isSelected = paletteState.selectedLenIndex == i;
        let classes = isSelected ? 'active ' : '';
        let label = '';
        switch (lenBean.len) {
            case BACKING_LEN_TYPE.LEN4: classes += 'len4'; label = '4'; break;
            case BACKING_LEN_TYPE.LEN8: classes += 'len8'; label = '8'; break;
            case BACKING_LEN_TYPE.LEN16: classes += 'len16'; label = '16'; break;
            case BACKING_LEN_TYPE.LEN2: classes += 'len2'; label = '2'; break;
            case BACKING_LEN_TYPE.LEN1: classes += 'len1'; label = '1'; break;
        }
        if (lenBean.isHalf) {
            label += ' .';
            classes += ' half';
        }
        if (lenBean.isDiv3) {
            label += ' /';
        }
        lens.push(
            <div
                key={i}
                className={classes}
                onClick={() => {
                    dispatcher.initBackingPalette({
                        ...paletteState,
                        selectedLenIndex: i
                    });
                }}>
                <span>{label}</span>
            </div>
        );
        const volume = curLayer.vels[i];
        volumeList.push(
            <div
                key={i}
                className={classes}
            >
                <div style={{ height: (volume * 10) + '%' }}></div>
            </div>
        );
    });

    return (
        <div className="backing-table">
            <div className="switch">
                <BackingOperation
                    dispatcher={dispatcher}
                    paletteState={paletteState}
                />
            </div>
            <div className="fixed">
                <div className="layer">
                    <button
                        className={
                            paletteState.sideIndex == 1 ? 'active' : ''
                        }
                        onClick={() => {
                            dispatcher.backing.setLayerIndex(1);
                        }}
                    >
                        <span>R-Layer</span>
                    </button>
                    <button
                        className={
                            paletteState.sideIndex == 0 ? 'active' : ''
                        }
                        onClick={() => {
                            dispatcher.backing.setLayerIndex(0);
                        }}
                    >
                        <span>L-Layer</span>
                    </button>
                </div>
                <div className="matrix">
                    {channelJsxList}
                </div>
                <div className="pedal">
                    <button
                        className={
                            paletteState.isUsePedal ? 'active' : ''
                        }
                        onClick={() => {
                            dispatcher.backing.setUsePedal(!paletteState.isUsePedal);
                        }}
                    >
                        <span>Pedal</span>
                    </button>
                </div>
            </div>
            <div className="main">
                <div className="split">
                    {lens}
                </div>
                <SplitMeasure
                    paletteState={paletteState}
                    layer={curLayer}
                />
                <NotesMatrix
                    paletteState={paletteState}
                    channelList={channelList}
                    dispatcher={dispatcher}
                />
                <div className="volume">
                    {volumeList}
                </div>
            </div>
        </div>
    );
}

export default BackingTable;