import { BackingEditorState, BackingLayer, BACKING_LEN_TYPE, DetailChord } from "contents/redux/store";
import { useRef } from "react";


type SplitMeasureProps = {
    paletteState: BackingEditorState;
    layer: BackingLayer;
}

const SplitMeasure = (props: SplitMeasureProps) => {

    const curLayer = props.layer;
    const paletteState = props.paletteState;
    const sustainLength = (paletteState.sustain + paletteState.headAdjust + paletteState.tailAdjust) * 4;

    // 
    const measureInner: JSX.Element[] = [];
    let measureLength = 0;
    curLayer.lens.forEach((lenBean, i) => {
        let cnt = 0;
        switch (lenBean.len) {
            case BACKING_LEN_TYPE.LEN1: cnt = 16; break;
            case BACKING_LEN_TYPE.LEN2: cnt = 8; break;
            case BACKING_LEN_TYPE.LEN4: cnt = 4; break;
            case BACKING_LEN_TYPE.LEN8: cnt = 2; break;
            case BACKING_LEN_TYPE.LEN16: cnt = 1; break;
        }
        if (lenBean.isHalf) {
            cnt *= 1.5;
        }
        measureLength += cnt;
    });
    // 
    const isLimit = measureLength > sustainLength;
    measureLength = (!isLimit ? sustainLength : measureLength);
    for (let i = 0; i < measureLength; i++) {
        const pos = i + (paletteState.headAdjust * 4);
        const getLen = () => {
            if (pos % 2 == 0) {
                if (pos % 4 == 0) {
                    return 'mem4';
                } else {
                    return 'mem8';
                }
            } else {
                return 'mem16';
            }
        }
        const isLimit = false;//i == sustainLength;
        measureInner.push(<div key={i} className={getLen() + (isLimit ? ' limit' : '')} />);
    }

    const limitLeft = sustainLength * 31;
    const limitJsx = !isLimit ?
        (<div style={{
            left: `${limitLeft}px`,
            width: '5px'
        }} className="safe"></div>)
        :
        (<div style={{
            left: `${limitLeft}px`,
            width: `${(measureLength - sustainLength) * 31}px`
        }} className="over"></div>);
    return (
        <div className="measure">
            {measureInner}
            {limitJsx}
        </div>
    );
}

export default SplitMeasure;